import * as mat4 from '../glMatrix/mat4';

import { T4V6V, UR287, KLUTF, CXAD0, KKEN4, CKUXB, GT6Y2, GI4VX, OASCO, SQ829 } from '../sceneConsts';

export function SQ4GC(TL63D) {
    this.OT1BU = TL63D;
    this.S58HW = TL63D.S58HW;
    
    var KEADL = 100;
    var IJ9EX = 60;

    var XDXL0 = 45;

    var ROIG8;
    var YKXQF;
    
    var O5SSS;
    var JUEEY;

    var UYT3X;

    this.SOGFT = [0.2, 0.2, 0.2, 1];
    this.GR3BS = [0.6, 0.6, 0.6, 1];

    this.N1QHO = [1,1,1];
    var YGIYW;
    var TQG45;
    var DY7ME;
    var SU03C;

    var K1S8Y = OASCO.ERMXX;

    this.GBNM5 = function() {
        ROIG8 = UR287(64, 64, [0, 0, 0]);
        YKXQF = UR287(32, 32, [0, XDXL0 + 10, 0]);

        O5SSS = UR287(64, 16, [0, 80, 0]);
        JUEEY = UR287(64, 16, [0, -80, 0]);

        this.VFQ0F = KLUTF([0, 0, 0], XDXL0 - 2);
        this.F1WPY = KLUTF([0, 0, 0], XDXL0 + 2);
        UYT3X = KLUTF([0, 0, 0], 18);
        
        this.OOV4D = GT6Y2(6);

        this.N1QHO = CKUXB(this.OOV4D);
        TQG45 = CKUXB(this.OOV4D + 1);
        YGIYW = CKUXB(this.OOV4D + 2);
        DY7ME = CKUXB(this.OOV4D + 3);
        SU03C = CKUXB(this.OOV4D + 4);
    };

    this.KX8KR = function (DLXD5, WV2JJ) {
        if (WV2JJ.RKI2K !== 0) return;
        
        K1S8Y = OASCO.ERMXX;

        if (DLXD5 === this.OOV4D) {
            K1S8Y = OASCO.SN1XC;
            WV2JJ.VUGJ9 = OASCO.SN1XC;
            WV2JJ.RKI2K = 1;
            return;
        }
        if (DLXD5 === (this.OOV4D + 1)) {
            K1S8Y = OASCO.LKDFL;
            WV2JJ.VUGJ9 = OASCO.LKDFL;
            WV2JJ.RKI2K = 1;
            return;
        }
        if (DLXD5 === (this.OOV4D + 2)) {
            K1S8Y = OASCO.WO9JC;
            WV2JJ.VUGJ9 = OASCO.WO9JC;
            WV2JJ.RKI2K = 1;
            return;
        }
        if (DLXD5 === (this.OOV4D + 3)) {
            K1S8Y = OASCO.D98EY;
            WV2JJ.VUGJ9 = OASCO.D98EY;
            WV2JJ.RKI2K = 1;
            return;
        }
        if (DLXD5 === (this.OOV4D + 4)) {
            K1S8Y = OASCO.TWVWB;
            WV2JJ.VUGJ9 = OASCO.TWVWB;
            WV2JJ.RKI2K = 1;
            return;
        }
        WV2JJ.RKI2K = 0;
    };

    this.KCBLT = function() {
        var OSCQO = this.OT1BU.GLTNM.DEWR1();
        var C3U66 = this.OT1BU.GLTNM.QCOBR();

        var AWAME = mat4.create();
        mat4.identity(AWAME);

        var XGW5I = mat4.create();
        mat4.identity(XGW5I);

        var QGHIE = mat4.create();
        mat4.identity(QGHIE);

        mat4.identity(AWAME);
        mat4.ortho(AWAME, -this.S58HW.viewportWidth / 2, this.S58HW.viewportWidth / 2, -this.S58HW.viewportHeight / 2, this.S58HW.viewportHeight / 2, -500, 500);

        var CMKXP = -this.S58HW.viewportWidth / 2 + IJ9EX;
        var Q6Q9A = -this.S58HW.viewportHeight / 2 + KEADL;

        mat4.identity(XGW5I);
        mat4.lookAt(XGW5I, [CMKXP, Q6Q9A, 500], [CMKXP, Q6Q9A, -500], [0, 1, 0]);

        mat4.identity(QGHIE);
        mat4.multiply(QGHIE, AWAME, XGW5I);
        this.S58HW.uniformMatrix4fv(this.OT1BU.QI65W, false, QGHIE);

        this.S58HW.depthFunc(this.S58HW.ALWAYS);

        this.S58HW.uniform1i(this.OT1BU.DQSLD, SQ829.P5KEV);
        this.S58HW.uniform1i(this.OT1BU.PQDJ6, SQ829.P5KEV);

        if (this.OT1BU.YUWA5) {
            this.OT1BU.Y6DKG.KCBLT(CXAD0(12, 14, XDXL0, OSCQO), this.N1QHO);
            this.OT1BU.Y6DKG.KCBLT(CXAD0(12, 14, XDXL0, OSCQO - Math.PI / 2.0), TQG45);
            this.OT1BU.Y6DKG.KCBLT(CXAD0(12, 14, XDXL0, OSCQO + Math.PI), YGIYW);
            this.OT1BU.Y6DKG.KCBLT(CXAD0(12, 14, XDXL0, OSCQO + Math.PI / 2.0), DY7ME);
            this.OT1BU.Y6DKG.KCBLT(UYT3X, SU03C);
        }
        else {
            this.OT1BU.RGPSB.KCBLT(this.F1WPY, this.GR3BS);
            this.OT1BU.RGPSB.KCBLT(this.VFQ0F, this.SOGFT);

            if (K1S8Y === OASCO.SN1XC) {
                this.OT1BU.Y6DKG.KCBLT(CXAD0(16, 18, XDXL0, OSCQO), T4V6V);
            }
            if (K1S8Y === OASCO.LKDFL) {
                this.OT1BU.Y6DKG.KCBLT(CXAD0(16, 18, XDXL0, OSCQO - Math.PI / 2.0), T4V6V);
            }
            if (K1S8Y === OASCO.WO9JC) {
                this.OT1BU.Y6DKG.KCBLT(CXAD0(16, 18, XDXL0, OSCQO + Math.PI), T4V6V);
            }
            if (K1S8Y === OASCO.D98EY) {
                this.OT1BU.Y6DKG.KCBLT(CXAD0(16, 18, XDXL0, OSCQO + Math.PI / 2.0), T4V6V);
            }
            if (K1S8Y === OASCO.TWVWB) {
                this.OT1BU.RGPSB.KCBLT(UYT3X, T4V6V);
            }
        }

        this.S58HW.uniform1i(this.OT1BU.DQSLD, SQ829.B0SJ4);
        this.S58HW.uniform1i(this.OT1BU.PQDJ6, SQ829.B0SJ4);

        if (!this.OT1BU.YUWA5) {
            this.OT1BU.Q81IG.EIU18();

            KKEN4(this.S58HW, this.OT1BU.GURJQ, this.VSI7I(OSCQO), this.OT1BU.C5GXA[GI4VX.HQEVC]);
            this.OT1BU.Q81IG.A9YUG(O5SSS, this.OT1BU.C5GXA[GI4VX.HQEVC]);

            KKEN4(this.S58HW, this.OT1BU.GURJQ, this.D5LD7(C3U66), this.OT1BU.C5GXA[GI4VX.HQEVC]);
            this.OT1BU.Q81IG.A9YUG(JUEEY, this.OT1BU.C5GXA[GI4VX.HQEVC]);

            this.OT1BU.Q81IG.A9YUG(ROIG8, this.OT1BU.C5GXA[GI4VX.HKWM2]);
            this.OT1BU.Q81IG.A9YUG(YKXQF, this.OT1BU.C5GXA[GI4VX.RXICX]);

            var PP_matrixNorth = CXAD0(12, 14, XDXL0, OSCQO);
            this.OT1BU.Q81IG.A9YUG(PP_matrixNorth, this.OT1BU.C5GXA[GI4VX.SPSK5]);

            var PP_matrixSouth = CXAD0(12, 14, XDXL0, OSCQO + Math.PI);
            this.OT1BU.Q81IG.A9YUG(PP_matrixSouth, this.OT1BU.C5GXA[GI4VX.GC014]);

            var PP_matrixEast = CXAD0(12, 14, XDXL0, OSCQO - Math.PI / 2.0);
            this.OT1BU.Q81IG.A9YUG(PP_matrixEast, this.OT1BU.C5GXA[GI4VX.RR7AJ]);

            var PP_matrixWest = CXAD0(12, 14, XDXL0, OSCQO + Math.PI / 2.0);
            this.OT1BU.Q81IG.A9YUG(PP_matrixWest, this.OT1BU.C5GXA[GI4VX.IOKYM]);

            var HYVW9 = -this.OT1BU.GLTNM.QCOBR();
            var PP_matrixLookDown = CXAD0(32, 32, XDXL0 - 22, HYVW9);
            this.OT1BU.Q81IG.A9YUG(PP_matrixLookDown, this.OT1BU.C5GXA[GI4VX.B65VA]);
            this.OT1BU.Q81IG.K6TCJ();
        }

        this.S58HW.depthFunc(this.S58HW.LESS);
    };

    this.VSI7I = function(OSCQO) {
        var S1IEE = -Math.floor(OSCQO * 180 / Math.PI);
        var V5L9A = '';

        if (S1IEE === 0) {
            V5L9A = 'North';
        }
        else if (S1IEE === -90) {
            V5L9A = 'East';
        }
        else if (S1IEE === 90) {
            V5L9A = 'West';
        }
        else if (S1IEE === -180 || S1IEE === 180) {
            V5L9A = 'South';
        }
        else if (S1IEE < 0 && S1IEE > -90) {
            V5L9A = 'N ' + (-S1IEE) + ' E';
        }
        else if (S1IEE < -90 && S1IEE > -180) {
            V5L9A = 'S ' + (180 + S1IEE) + ' E';
        }
        else if (S1IEE < 90 && S1IEE > 0) {
            V5L9A = 'N ' + S1IEE + ' W';
        }
        else if (S1IEE < 180 && S1IEE > 90) {
            V5L9A = 'S ' + (180 - S1IEE) + ' W';
        }

        return V5L9A;
    };

    this.D5LD7 = function(C3U66) {
        var S1IEE = Math.floor(C3U66 * 180 / Math.PI);
        var V5L9A = '';

        if (S1IEE > 90) {
            V5L9A = 'Up ' + (S1IEE - 90);
        }
        else {
            V5L9A = 'Down ' + (90-S1IEE);
        }
        return V5L9A;
    }    
}
