import React from "react";
import FlexLayout from "flexlayout-react";
import { PetroContext } from './context/petroContext';
// import {Button} from '@material-ui/core';
import MapListComponent from "./components/mapList";
import MapContentComponent from "./components/mapContent";
import MapInfoComponent from "./components/mapInfo";
import HeaderComponent from "./header";
import { KLXR3 } from './services/projectService';

// import {
//   BrowserRouter as Router,
//   Switch,
//   Route,
//   Link,
//   useParams,
//   useRouteMatch
// } from "react-router-dom";

import "./styles.css";
import "./flexlayout-light.css";

class DashboardComponent extends React.Component {
  static contextType = PetroContext;

  constructor(props) {
    super(props);

    let dataFile = require("./dashboardLayout.json");
    this.state = {
      model: FlexLayout.Model.fromJson(dataFile),
    };
  }

  componentDidMount() {
    const { Token, dispatchAction } = this.context;
    if (Token) {
        KLXR3.BEQ1L(Token).then(resp => {
          dispatchAction({type:"SET_PROJECTS", data: resp.data})
      })
    }

  }

  factory(node) {
    const { L537N } = this.context;
    var component = node.getComponent();
    if (component === "Header") {
      return (
        <div style={{ background: "green" }}>
          Panel {node.getName()}
        </div>
      );
    }
    else if (component === "Project") {
      return (
        <div style={{ background: "white", padding: '6px' }}>
          <MapContentComponent />
        </div>
      );
    }
    else if (component === "map") {
      return (
          <MapListComponent projects={L537N} />
      );
    }
    else if (component === "Info") {
      return (
        <div style={{ background: "white", padding: '6px' }} className="panelProject">
           <MapInfoComponent />
        </div>
      );
    }

  }

  render() {
    return (
      <div>
        <HeaderComponent />

        <FlexLayout.Layout
          model={this.state.model}
          factory={this.factory.bind(this)}
        />

      </div>
    );
  }
}

export default DashboardComponent;

