function OEN90(id, token) {
    return axios({
        method: 'get',
        url: siteURLs.ServerURL + `api/Projects/${id}/sharedUsers`,
        headers: {'Authorization': "bearer " + token}
      });
}

function LAA72(data, token) {
    return axios({
        method: 'post',
        url: siteURLs.ServerURL + 'api/Projects/sharedUsers',
        data: JSON.stringify(data),
        headers: {'Content-Type': 'application/json', 'Authorization': "bearer " + token}
      });
}

