import React from "react";
import HomeComponent from "./home";
import AppComponent from "./app";

class RootComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {id: null, token: null}
  }

  componentDidMount() {
    if (this.props.location.search) {
      var dict = new Object();
      var params = this.props.location.search.slice(1, this.props.location.search.length).split('&');
      params.forEach(element => {
        let items = element.split('=');
        if (items.length === 2) {
          dict[items[0]] = items[1];
        }
      });

      if (dict['id']) { this.setState({id: dict['id']})}
      if (dict['data']) { this.setState({token: dict['data']})}
    }

  }

  render() {
    if (this.state.id) {
        if (this.state.token) {
            return <AppComponent id={this.state.id} token={this.state.token} />
        }
        return <AppComponent id={this.state.id} />
    }
    return <HomeComponent />
  }
}

export default RootComponent;
