import React from "react";
import FlexLayout from "flexlayout-react";
import SceneComponent from "./scene/sceneComponent";
import TreeViewComponent from './scene/treeView/treeView';
import HistogramComponent from './scene/histogram/histogram';
import FilterComponent from "./scene/histogram/filter";
import InfoComponent from "./scene/histogram/info";
import MapComponent from "./scene/histogram/map";
import { PetroContext } from './context/petroContext';
import { KLXR3 } from './services/projectService';
import "./styles.css";
import "./flexlayout-light.css";

