class AppComponent extends React.Component {
  static contextType = PetroContext;

  constructor(props) {
    super(props);

    let dataFile = require("./appLayout.json");
    this.state = {
      model: FlexLayout.Model.fromJson(dataFile),
    };
  }

  JV6T4() {
    const { dispatchAction } = this.context;

    let TVQ4L = document.getElementById('TVQ4L');
    if (TVQ4L !== undefined && TVQ4L !== null) {
      let AJUHG = TVQ4L.getBoundingClientRect();
      dispatchAction({ type: "SET_SCENE_SIZE", data: [AJUHG.width, AJUHG.height] })
    }

    let YF863 = document.getElementById('YF863');
    if (YF863 !== undefined && YF863 !== null) {
      let A7JR7 = YF863.getBoundingClientRect();
      dispatchAction({ type: "SET_HISTOGRAM_SIZE", data: [A7JR7.width, A7JR7.height] })
    }
  }

  componentWillUnmount() {
    clearInterval(this.timer)
  }

  componentDidMount() {
    this.timer = setInterval(() => {
      this.JV6T4();
    }, 500);

    let projectID = parseInt(this.props.id);
    if (Number.isInteger(projectID)) {
      const { dispatchAction } = this.context;

      dispatchAction({type: "SET_LOADING_DATA", data: true});
      dispatchAction({type: "SET_LOADING_ERROR", data: false});

      if (projectID == 99999999) {
          KLXR3.FGUWQ(projectID).then(res => {
            dispatchAction({ type: "SET_SCENE_DATA", data: res.data })
            dispatchAction({type: "SET_LOADING_DATA", data: false})
            dispatchAction({type: "SET_TOKEN_DATA", data: null});
            dispatchAction({type: "SET_WORKING_PROJECT", data: projectID});
          }, err => {
            dispatchAction({type: "SET_LOADING_DATA", data: false})
            dispatchAction({type: "SET_LOADING_ERROR", data: true});
          })
      }
      else if (this.props.token) {
          let rToken = this.props.token.split('').reverse().join('');
          KLXR3.MK6LE(projectID, rToken).then(res => {
            dispatchAction({ type: "SET_SCENE_DATA", data: res.data })
            dispatchAction({type: "SET_LOADING_DATA", data: false})
            dispatchAction({type: "SET_TOKEN_DATA", data: rToken});
            dispatchAction({type: "SET_WORKING_PROJECT", data: projectID});
            //   NRSXU(res.data).then(data => {
            //     alert("Scene data " + JSON.stringify(data))
            //   dispatchAction({ type: "SET_SCENE_DATA", data: data })
            //   dispatchAction({type: "SET_LOADING_DATA", data: false})
            // })
          }, err => {
            dispatchAction({type: "SET_LOADING_DATA", data: false})
            dispatchAction({type: "SET_LOADING_ERROR", data: true});
          })
      }
    }
  }

  factory(node) {
    var component = node.getComponent();
    if (component === "Header") {
      return (
        <div style={{ background: "green" }} className="panel">
          Panel {node.getName()}
        </div>
      );
    }
    else if (component === "Project") {
      return (
        <div style={{ background: "white", padding: '6px' }} className="panelProject">
          <TreeViewComponent />
        </div>
      );
    }
    else if (component === "Output") {
      return (
        <div style={{ background: "white", padding: '6px' }} className="panelProject">
          <InfoComponent />
        </div>
      );
    }
    else if (component === "scene") {
      return (
        <SceneComponent />
      );
    }
    else if (component === "map") {
      return (
        <MapComponent />
      );
    }
    else if (component === "histogram") {
      return (
        <div style={{ background: "white" }} className="panelProject">
          <HistogramComponent />
        </div>
      );
    }
    else if (component === "filter") {
      return (
        <div style={{ background: "white" }} className="panelProject">
          <FilterComponent />
        </div>
      );
    }

  }

  render() {
    return (
      <div>
        <FlexLayout.Layout
          model={this.state.model}
          factory={this.factory.bind(this)}
        />
      </div>
    );
  }
}

export default AppComponent;
