import React, { useEffect, useState, useContext, Fragment }  from "react";
import { PetroContext } from '../context/petroContext';

const MapInfoComponent = (props) => {
  const { RAHLT, L537N } = useContext(PetroContext)
  const [name, setName] =useState('')
  const [description, setDescription] =useState('')

  useEffect(() => {
      let activeProject = L537N.find(item => item.id === RAHLT);
      if (activeProject !== undefined) {
          setDescription(activeProject.description);
          setName(activeProject.name);
      }
  }, [RAHLT])

  return (
    <Fragment>
      <div style={{ width: "100%", height: "100%" }}>
          <div><span style={{fontWeight:"bold"}}>{name} project description</span></div>
          <div><span>{description}</span></div>
      </div>
    </Fragment>
  );
}


export default MapInfoComponent;