export const petroReducer = (state, action) => {
  switch (action.type) {
    case 'SET_SCENE_DATA':
        return Object.assign( {}, state, { AU16L: action.data });    
    case 'SET_GEOLOCATION':
      return Object.assign( {}, state, { BG9YH: action.data });    
    case 'SET_TREEVIEW':
      return Object.assign( {}, state, { BK7BT: action.data });
    case 'SET_TREEVIEW_VISIBLE':
        return Object.assign( {}, state, { YWRJY: action.data });    
    case 'SET_EVENTDATA':
      return Object.assign( {}, state, { NMH4H: action.data });
    case 'SET_FRACTUREDATA':
      return Object.assign( {}, state, { UWS2D: action.data });
    case 'SET_EVENTTIME':
      return Object.assign( {}, state, { X9PG3: action.data[0], FBAYS: action.data[1] });
    case 'SET_SCENE_SIZE':
      return Object.assign( {}, state, { DTICP: action.data });
    case 'SET_HISTOGRAM_SIZE':
      return Object.assign( {}, state, { XLWX7: action.data });           
    case 'SET_MAGNITUDE_DATA':
      return Object.assign( {}, state, { H1991: action.data });           
    case 'SET_CONFIDENCE_DATA':
      return Object.assign( {}, state, { TA8NY: action.data });           
    case 'SET_OBJECTINFO_DATA':
      return Object.assign( {}, state, { LDLPU: action.data });           
    case 'SET_LOADING_DATA':
      return Object.assign( {}, state, { AFV32: action.data });           
    case 'SET_LOADING_ERROR':
      return Object.assign( {}, state, { YIUIM: action.data });           
    case 'SET_ACTIVE_PROJECT':
        return Object.assign( {}, state, { RAHLT: action.data });  
    case 'SET_WORKING_PROJECT':
        return Object.assign( {}, state, { FCYDC: action.data });  
    case 'SET_MAPCENTER':
      return Object.assign({}, state, {AAINL: action.data});                 
    case 'USER_LOGIN':
        return Object.assign({}, state, {CurrentUser: action.data});
    case 'SET_TOKEN_DATA':
        return Object.assign({}, state, {Token: action.data});
    case 'USER_LOGOUT':
      return Object.assign({}, state, {CurrentUser: null, Token: null, L537N: []});
    case 'SET_PROJECTS':
      return Object.assign({}, state, {L537N: action.data})
    default:
      return state;
  }
} 