import React, { useEffect, useState, useContext, Fragment }  from "react";
import { PetroContext } from '../context/petroContext';
import {Button} from '@material-ui/core';
import { ArrowDropUp, ArrowDropDown, Delete, Edit, Visibility, Add, Share } from '@material-ui/icons';
import NewProjectDialog from './dialogs/newProjectDialog';
import ConfirmDialog from './dialogs/confirmDialog';
import ShareProjectDialog from './dialogs/shareProjectDialog';
import { KLXR3 } from '../services/projectService';
import { withRouter } from "react-router-dom";
import "./map.css";

const SortBy = {
  ID_ASC: 0,
  ID_DSC: 1,
  NAME_ASC: 2,
  NAME_DSC: 3,
  TIME_ASC: 4,
  TIME_DSC: 5,
  PLACE_ASC: 6,
  PLACE_DSC: 7
}

const MapContentComponent = (props) => {
  const [sortMethod, setSortMethod] = useState(SortBy.ID_ASC);
  const [projectList, setProjectList] = useState([]);
  const [projectOpen, setProjectOpen] = useState(false);
  const [shareDlgOpen, setShareDlgOpen] = useState(false);
  const [confirmOpen, setConfirmOpen] = useState(false);
  const [activeProject, setActiveProject] = useState(null);
  const [createMode, setCreateMode] = useState(true);
  const { CurrentUser, L537N, dispatchAction, RAHLT, Token } = useContext(PetroContext)

  const sortProject = (sortMethod) => {
    if (sortMethod === SortBy.ID_ASC) {
      setProjectList(L537N.sort((a, b) => { return a.id - b.id; }))
    }
    else if (sortMethod === SortBy.ID_DSC) {
      setProjectList(L537N.sort((b, a) => { return a.id - b.id; }))
    }
    else if (sortMethod === SortBy.NAME_ASC) {
      setProjectList(L537N.sort((a, b) => { return (a.name > b.name) ? 1 : ((b.name > a.name) ? -1 : 0) }));
    }
    else if (sortMethod === SortBy.NAME_DSC) {
      setProjectList(L537N.sort((b, a) => { return (a.name > b.name) ? 1 : ((b.name > a.name) ? -1 : 0) }));
    }
    else if (sortMethod === SortBy.TIME_ASC) {
      setProjectList(L537N.sort((a, b) => { return (a.date > b.date) ? 1 : ((b.date > a.date) ? -1 : 0) }));
    }
    else if (sortMethod === SortBy.TIME_DSC) {
      setProjectList(L537N.sort((b, a) => { return (a.date > b.date) ? 1 : ((b.date > a.date) ? -1 : 0) }));
    }
    else if (sortMethod === SortBy.PLACE_ASC) {
      setProjectList(L537N.sort((a, b) => { return (a.place > b.place) ? 1 : ((b.place > a.place) ? -1 : 0) }));
    }
    else if (sortMethod === SortBy.PLACE_DSC) {
      setProjectList(L537N.sort((b, a) => { return (a.place > b.place) ? 1 : ((b.place > a.place) ? -1 : 0) }));
    }
    else {
      setProjectList(L537N.sort((a, b) => { return a.id - b.id; }))
    }
    setSortMethod(sortMethod)
  }

  useEffect(() => {
    KLXR3.BEQ1L(Token).then(resp => {
      dispatchAction({type:"SET_PROJECTS", data: resp.data})
      }, error => {
          alert('Error: fail to refresh project list')
      }) 
  }, []);

  useEffect(() => {
    sortProject(sortMethod);
  }, [L537N]);

  const openProject = (id) => {
    if (Token) {
      let rToken = Token.split('').reverse().join('');
      let newWindow = window.open(window.location.origin + `/?id=${id}&data=${rToken}`, "_blank", "fullscreen=1, location=0, toolbar=0, menubar=0, resizable=1");
      newWindow.moveTo(0,0);
      newWindow.resizeTo(window.screen.width, window.screen.height)
    }    
  }

  const getRow = (element) => {
    return (
      <div className={RAHLT === element.id ? "tableActiveRow" : "tableRow"} onClick={() => {
        dispatchAction({type:'SET_ACTIVE_PROJECT', data: element.id});
        dispatchAction({type:'SET_MAPCENTER', data: {lat: element.lat, lng: element.lng}});
        }}>
          <div className="tableCell" style={{width: 60, marginRight: 10, flexGrow: 0}} >{element.id}</div>
          <div className="tableCell" style={{width: 100, marginRight: 10}}>{element.name}</div>
          <div className="tableCell" style={{width: 100, marginRight: 10}}>{new Date(element.date).toLocaleDateString("en-US")} </div>
          <div className="tableCell" style={{width: 100, marginRight: 10}}>{element.place}</div>
      </div>
    )
  }

  const deleteProject = () => {
    KLXR3.AV4M5(activeProject.id, Token).then(res => {
      KLXR3.BEQ1L(Token).then(resp => {
          dispatchAction({type:"SET_PROJECTS", data: resp.data})
        }, error => {
          alert('Error: fail to refresh project list')
        })    
      }, err => {
        alert('Error: fail to delete project')
      }
    );
    setConfirmOpen(false);
  }
  
return (<div style={{ width: "100%" }} >
        <div style={{display:'flex', flexDirection:'row', justifyContent:'space-between', minWidth:'500px', margin: "16px 0px"}}>
          <Button style={{float:'left', textTransform:'none'}}  size="medium" variant="contained" startIcon={<Add />} color="primary"
                      onClick={() => {setCreateMode(true); setProjectOpen(true)}} >New project
          </Button>
          {RAHLT > 0 &&
          <div style={{display:'flex', flexDirection:'row', justifyContent:'flex-end'}}>
            <Button size="small" variant="contained" color="primary" style={{marginRight: 1, textTransform:'none'}} startIcon={<Visibility />} onClick={() => {openProject(RAHLT)}} >View</Button>
            
            { CurrentUser.role !== "User" &&
            <Fragment>
            <Button startIcon={<Share />} style={{backgroundColor: "#2E8B57", color:"white", marginRight: 1, textTransform:'none'}} onClick={() => {                  
                  let selectedProject = projectList.find(element => element.id === RAHLT);
                  if (selectedProject) {
                      setActiveProject(selectedProject);
                      setShareDlgOpen(true);              
                  }}} >Share</Button>
            <Button size="small" variant="contained" color="primary" style={{marginRight: 1, textTransform:'none'}} startIcon={<Edit />} onClick={() => {
              let selectedProject = projectList.find(element => element.id === RAHLT);
              if (selectedProject) {
                  setCreateMode(false);
                  setActiveProject(selectedProject);
                  setProjectOpen(true);              
              }
              }} >Edit</Button>
            <Button startIcon={<Delete />} style={{backgroundColor: "#F3005A", color:"white", marginRight: 1, textTransform:'none'}} onClick={() => {
              let selectedProject = projectList.find(element => element.id === RAHLT);
              if (selectedProject) {
                  setActiveProject(selectedProject);
                  setConfirmOpen(true);              
              }}} >Delete</Button>
              </Fragment>
            }
          </div>}
        </div>

        <div className="headerRow">
          <div className="headerCell" style={{width: 60, marginRight: 10, flexGrow: 0}} 
             onClick={() => { sortMethod === SortBy.ID_ASC ? sortProject(SortBy.ID_DSC) : sortProject(SortBy.ID_ASC)}}
          >
            <div className="headerMenu">
              <span>Id</span> {sortMethod === SortBy.ID_ASC ? <ArrowDropUp /> : (sortMethod === SortBy.ID_DSC ? <ArrowDropDown /> : "")}
            </div>
         </div>

          <div className="headerCell" style={{width: 100, marginRight: 10}}
             onClick={() => { sortMethod === SortBy.NAME_ASC ? sortProject(SortBy.NAME_DSC) : sortProject(SortBy.NAME_ASC)}}
          >
            <div className="headerMenu">
            <span>Name</span> {sortMethod === SortBy.NAME_ASC ? <ArrowDropUp /> : (sortMethod === SortBy.NAME_DSC ? <ArrowDropDown /> : "")}
            </div>            
          </div>

          <div className="headerCell" style={{width: 100, marginRight: 10}}
             onClick={() => { sortMethod === SortBy.TIME_ASC ? sortProject(SortBy.TIME_DSC) : sortProject(SortBy.TIME_ASC)}}
          >
            <div className="headerMenu">
            <span>Date</span> {sortMethod === SortBy.TIME_ASC ? <ArrowDropUp /> : (sortMethod === SortBy.TIME_DSC ? <ArrowDropDown /> : "")}
            </div>
          </div>

          <div className="headerCell" style={{width: 100, marginRight: 10}}
             onClick={() => { sortMethod === SortBy.PLACE_ASC ? sortProject(SortBy.PLACE_DSC) : sortProject(SortBy.PLACE_ASC)}}
          >
            <div className="headerMenu">
             <span>Place</span> {sortMethod === SortBy.PLACE_ASC ? <ArrowDropUp /> : (sortMethod === SortBy.PLACE_DSC ? <ArrowDropDown /> : "")}
             </div>
          </div>
      </div>

          {
              projectList && projectList.map(element => {
                  return getRow(element)
              })
          }

          
          <NewProjectDialog open={projectOpen} create={createMode} project={activeProject} handleDialogClose={() => setProjectOpen(false)} />

          { activeProject &&
          <ConfirmDialog open={confirmOpen} title="Delete confirmation" content={"Are you sure to delete (" + activeProject.id + ") " + activeProject.name + " project?"} 
          handleConfirmAction={deleteProject} handleConfirmCancel={() => {setConfirmOpen(false)}} />
          }

          { activeProject &&
          <ShareProjectDialog open={shareDlgOpen} projectName={activeProject.name} projectId={activeProject.id} handleDialogClose={() => {
              setShareDlgOpen(false);
          }} />
          }
        </div>);
}

export default withRouter(MapContentComponent);