import * as vec3 from './glMatrix/vec3';

export function EN5DO() {
    this.GBNM5 = function (TT5J3, TUNUX, Q3I43)
    {
        TT5J3 = TT5J3 * Math.PI / 180;
        TUNUX = TUNUX * Math.PI / 180;

        this.G24VJ = TT5J3;
        this.ALCL2 = TUNUX;

        this.UE143 = 0.081819191 * 0.081819191;
        this.RBTYO = 6378137.0;
        this.KI4RV = 6356752.3142;

        this.XVMVR = (this.RBTYO * this.RBTYO - this.KI4RV * this.KI4RV) / (this.RBTYO * this.RBTYO);
        this.WX6L7 = (this.RBTYO * this.RBTYO - this.KI4RV * this.KI4RV) / (this.KI4RV * this.KI4RV);

        this.SJQN6 = this.EJFNT(TT5J3, TUNUX, Q3I43);
        let M0RVO = this.EJFNT(TT5J3, TUNUX, Q3I43 + 1000);

        this.N84N8 = vec3.create();
        vec3.subtract(this.N84N8, M0RVO, this.SJQN6);
        vec3.normalize(this.N84N8, this.N84N8);

        this.O9YXI = vec3.create();
        vec3.cross(this.O9YXI, [0, 0, 1], this.N84N8);
        vec3.normalize(this.O9YXI, this.O9YXI);

        this.VT794 = vec3.create();
        vec3.cross(this.VT794, this.N84N8, this.O9YXI);
        vec3.normalize(this.VT794, this.VT794);

        this.F92TI = Q3I43;
    }

    this.TJW24 = function (NOR65, NNNC5, XSIK3) {
        let W5QRO = vec3.create();
        vec3.scale(W5QRO, this.O9YXI, NOR65);

        let HJG3C = vec3.create();
        vec3.scale(HJG3C, this.VT794, NNNC5);

        let JQPC3 = vec3.create();
        vec3.scale(JQPC3, this.N84N8, XSIK3);

        vec3.add(W5QRO, this.SJQN6, W5QRO);
        vec3.add(W5QRO, HJG3C, W5QRO);
        vec3.add(W5QRO, JQPC3, W5QRO);

        W5QRO = this.T690B(W5QRO[0], W5QRO[1], W5QRO[2]);

        W5QRO[0] *= 180 / Math.PI;
        W5QRO[1] *= 180 / Math.PI;

        let CJ991 = 100000000;
        W5QRO[0] = Math.floor(W5QRO[0] * CJ991) / CJ991;
        W5QRO[1] = Math.floor(W5QRO[1] * CJ991) / CJ991;

        let W9RC8 = this.A33EB(W5QRO[0], false);
        let XPRC4 = this.A33EB(W5QRO[1], true);
        return [W5QRO[0], W5QRO[1], this.F92TI + XSIK3, W9RC8, XPRC4];
    }

    this.FM5BH = function (RV1F9) {
        let M7WMQ = RV1F9 * 180 / Math.PI;

        let CJ991 = 100000000;
        M7WMQ = Math.floor(M7WMQ * CJ991) / CJ991;
        return M7WMQ;
    }

    this.A33EB = function(RV1F9, YVMXQ) {
        let M7WMQ = 0;
        let YF7X5 = 0;
        let ESPU9 = 0;

        let FEQQI = Math.abs(RV1F9);
    
        M7WMQ = Math.floor(FEQQI);
    
        ESPU9 = (FEQQI - M7WMQ) * 3600;
    
        YF7X5 = Math.floor(ESPU9 / 60);
    
        ESPU9 = Math.floor((ESPU9 - (YF7X5 * 60)) * 100) / 100;

        // if (RV1F9 < 0)
        // {
        //     if (YVMXQ)
        //     {
        //         return M7WMQ + '°' + YF7X5 + '′' + ESPU9 + '"' + ' S';
        //     }
        //     else
        //     {
        //         return M7WMQ + '°' + YF7X5 + '′' + ESPU9 + '"' + ' W';
        //     }
        // }
        // else
        // {
        //     if (YVMXQ) {
        //         return M7WMQ + '°' + YF7X5 + '′' + ESPU9 + '"' + ' N';
        //     }
        //     else {
        //         return M7WMQ + '°' + YF7X5 + '′' + ESPU9 + '"' + ' E';
        //     }
        // }
    }

    this.EJFNT = function(TT5J3, TUNUX, XSIK3)
    {
        let n = this.RBTYO / Math.sqrt(1 - this.UE143 * Math.sin(TUNUX) * Math.sin(TUNUX));
        let U0GQ4 = (n + XSIK3) * Math.cos(TUNUX) * Math.cos(TT5J3);
        let XV5U6 = (n + XSIK3) * Math.cos(TUNUX) * Math.sin(TT5J3);
        let RREWP = (XSIK3 + (1 - this.UE143) * n) * Math.sin(TUNUX);

        return [U0GQ4, XV5U6, RREWP];
    }

    this.T690B = function (Y1ECK, R9WV5, QTWM7)
    {
        let P71K3 = Math.atan2(R9WV5, Y1ECK);

        let W6A4X = Math.sqrt(Y1ECK * Y1ECK + R9WV5 * R9WV5);
        let EIW1Q = Math.atan2(QTWM7 * this.RBTYO, W6A4X * this.KI4RV);

        let TY07P = Math.sin(EIW1Q);
        let MM2A7 = Math.cos(EIW1Q);

        let D6XHV = Math.atan2(QTWM7 + this.WX6L7 * this.KI4RV * TY07P * TY07P * TY07P,
                         W6A4X - this.XVMVR * this.RBTYO * MM2A7 * MM2A7 * MM2A7);

        let d = Math.sin(D6XHV);

        let M5SFI = W6A4X / Math.cos(D6XHV) - this.RBTYO / Math.sqrt(1 - this.XVMVR * d * d);

        return [P71K3, D6XHV, M5SFI];
    }


}