import { SJG39 } from './gridPlane';

export function EYVSG(TL63D) {
    this.OT1BU = TL63D;
    this.S58HW = TL63D.S58HW;
    this.UUF2O = [0.02, 0.02, 0.02];
    this.EBSYA = [0.4, 0.4, 0.4];
    this.ROWKQ = true;
    
    var YTUBN = [];

    this.GBNM5 = function (VKYEP, J0LLP, OP59G, HL2M4, PIK91, E9C0E, OTYYS) {
        this.OTYYS = OTYYS;

        this.VKYEP = VKYEP; this.J0LLP = J0LLP; this.OP59G = OP59G;
        this.HL2M4 = HL2M4; this.PIK91 = PIK91; this.E9C0E = E9C0E;

        let L7YXS = new SJG39(this.OT1BU, 'West');
        L7YXS.GBNM5([this.VKYEP, this.J0LLP, this.E9C0E],
                       [this.VKYEP, this.PIK91, this.E9C0E],
                       [this.VKYEP, this.PIK91, this.OP59G],
                       [this.VKYEP, this.J0LLP, this.OP59G],
                       this.UUF2O,
                       this.EBSYA,
                       this.OTYYS);
        YTUBN.push(L7YXS);

        let IDUCJ = new SJG39(this.OT1BU, 'North');
        IDUCJ.GBNM5([this.VKYEP, this.J0LLP, this.OP59G],
                       [this.VKYEP, this.PIK91, this.OP59G],
                       [this.HL2M4, this.PIK91, this.OP59G],
                       [this.HL2M4, this.J0LLP, this.OP59G],
                       this.UUF2O,
                       this.EBSYA,
                       this.OTYYS);
        YTUBN.push(IDUCJ);

        let DU4DV = new SJG39(this.OT1BU, 'East');
        DU4DV.GBNM5([this.HL2M4, this.J0LLP, this.OP59G],
                        [this.HL2M4, this.PIK91, this.OP59G],
                        [this.HL2M4, this.PIK91, this.E9C0E],
                        [this.HL2M4, this.J0LLP, this.E9C0E],
                        this.UUF2O,
                        this.EBSYA,
                        this.OTYYS);
        YTUBN.push(DU4DV);

        let INGKS = new SJG39(this.OT1BU, 'South');
        INGKS.GBNM5([this.HL2M4, this.J0LLP, this.E9C0E],
                       [this.HL2M4, this.PIK91, this.E9C0E],
                       [this.VKYEP, this.PIK91, this.E9C0E],
                       [this.VKYEP, this.J0LLP, this.E9C0E],
                       this.UUF2O,
                       this.EBSYA,
                       this.OTYYS);
        YTUBN.push(INGKS);

        let R47BC = new SJG39(this.OT1BU, 'Top');
        R47BC.GBNM5([this.VKYEP, this.PIK91, this.OP59G],
                      [this.VKYEP, this.PIK91, this.E9C0E],
                      [this.HL2M4, this.PIK91, this.E9C0E],
                      [this.HL2M4, this.PIK91, this.OP59G],
                      this.UUF2O,
                      this.EBSYA,
                      this.OTYYS);
        YTUBN.push(R47BC);

        let X5EUK = new SJG39(this.OT1BU, 'Bottom');
        X5EUK.GBNM5([this.VKYEP, this.J0LLP, this.E9C0E],
                         [this.VKYEP, this.J0LLP, this.OP59G],
                         [this.HL2M4, this.J0LLP, this.OP59G],
                         [this.HL2M4, this.J0LLP, this.E9C0E],
                         this.UUF2O,
                         this.EBSYA,
                         this.OTYYS);
        YTUBN.push(X5EUK);

        var BVGMC = this.HL2M4 - this.VKYEP;
        var K1T2R = this.E9C0E - this.OP59G;

        var N7TAN = Math.sqrt(BVGMC * BVGMC + K1T2R * K1T2R);
        var EI62E = Math.floor(N7TAN / this.OTYYS);
        EI62E = Math.floor(EI62E / 2);
        if (EI62E < 1) EI62E = 1;
        N7TAN = EI62E * this.OTYYS;
    };

    this.KCBLT = function () {
        this.S58HW.uniform1i(this.OT1BU.DQSLD, 1);
        this.S58HW.uniform1i(this.OT1BU.PQDJ6, 1);

        for (var i = 0; i < YTUBN.length; i++) {
            YTUBN[i].KCBLT();
        }

        this.S58HW.uniform1i(this.OT1BU.DQSLD, 0);
        this.S58HW.uniform1i(this.OT1BU.PQDJ6, 0);
    };

}



