import React, { Fragment, useContext, useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { MPTL2 } from '../services/userService';
import { Redirect, withRouter, Link } from "react-router-dom";
import { PetroContext } from '../context/petroContext';
import {Button} from '@material-ui/core';
import HeaderComponent from '../header';
import FooterComponent from '../footer';
import "../styles.css";

const useStyles = makeStyles({
    root: {
      boxShadow: '0 0 6px 6px rgba(200, 200, 200, .3)',
      width:"400px", 
      margin:"auto", 
      padding: "20px"
    },
    title: {
        fontSize: '30px',
        fontWeight:'bold',
        marginTop: '20px',
        marginBottom:"30px",
    },
    label: {
        marginTop: '20px',
        fontSize: '20px',
    },
    input: {
        fontSize: '16px',
        width: '100%',
        padding: '6px',
        marginTop: '10px',
        marginBottom:'10px'
    },
    sumbit: {
        marginTop: '20px',
        textAlign:'center',
    },
    error: {
        color: 'red'
    }
  });


const LoginComponent = (props) => {
    const classes = useStyles();
    const [loginError, setLoginError] = useState(false)
    const { CurrentUser, dispatchAction } = useContext(PetroContext);

    return (
        CurrentUser ? <Redirect to='/' />
         :
      <Fragment>
        <HeaderComponent />
        <div style={{paddingTop: '170px', paddingLeft:"10%", paddingRight:"10%", paddingBottom:'20px' }}>
            <div className={classes.root} >
            <div className={classes.title} >Sign In</div>
                <Formik
                    initialValues={{
                        username: '',
                        password: ''
                    }}
                    validationSchema={Yup.object().shape({
                        username: Yup.string().required("Email is reuqired").email('Invalid email'),
                        password: Yup.string().required('Password is required').min(3, 'Minimum 6 characters').max(30, 'Maximum 30 characters')
                    })}
                    onSubmit={({ username, password }, { setStatus, setSubmitting }) => {
                        setStatus();
                        MPTL2.D6IDC(username, password).then(resp => {
                            dispatchAction({type:'USER_LOGIN', data:resp.data});
                            dispatchAction({type:'SET_TOKEN_DATA', data:resp.data.token});
                            localStorage.setItem("loginData", JSON.stringify(resp.data));
                            localStorage.setItem("loginTime", new Date().getTime());
                            props.history.push('/projects')
                        }, error => { setLoginError(true)});
                    }}
                    render={({ errors, status, touched, isSubmitting }) => (
                        <Form>                          
                            <div>
                                <span className={classes.label}>Email</span>
                            </div>
                            <div>
                                <Field name="username" type="text" className={classes.input} />
                            </div>
                            <div>
                                <ErrorMessage name="username" component="div" className={classes.error} />
                            </div>

                            <div style={{marginTop:'20px'}}>
                                <span className={classes.label}>Password</span>
                            </div>
                            <div>
                                <Field name="password" type="password" className={classes.input} />
                            </div>
                            <div>
                                <ErrorMessage name="password" component="div" className={classes.error} />
                            </div>

                            {loginError && <div className={classes.error}>Fail to sign in to your account</div>}
                            <div className={classes.sumbit}>
                                <Button type="submit" variant="contained" color="primary" style={{width:'100%', textTransform:'capitalize', backgroundColor:'#0B74A4', color:'#FFFFFF'}}>
                                    Sign In
                                </Button>
                            </div>
                            <div style={{marginTop:'20px', display:'flex', flexDirection:'row', 
                            justifyContent:'space-between', marginBottom:'20px'}}>
                                <Link style={{ textDecoration: 'none', backgroundColor: "#FFFFFF00" }} to="/forgetpassword">
                                    <Button variant="contained" color="default" style={{textTransform:'capitalize'}}>
                                        Forget password?
                                    </Button>
                                </Link>
                                <Link style={{ textDecoration: 'none', backgroundColor: "#FFFFFF00" }} to="/resetpassword">
                                    <Button variant="contained" color="default" style={{textTransform:'capitalize'}}>
                                        Reset password
                                    </Button>
                                </Link>
                            </div>
                            <hr />
                            <div className={classes.sumbit} style={{textAlign:'left', marginBottom:'20px'}}>
                            <span>New?</span>
                            </div>
                            <div className={classes.sumbit}>
                                <Link style={{ textDecoration: 'none', backgroundColor: "#FFFFFF00" }} to="/signup">
                                    <Button variant="contained" color="primary" style={{ width: '100%', textTransform: 'capitalize', backgroundColor: '#2E8B57', color: '#FFFFFF' }}>
                                        Create Account
                                    </Button>
                                </Link>
                            </div>

                        </Form>
                    )}
                />                

            </div>
        </div>
        <FooterComponent />
      </Fragment>   
    );

}

export default withRouter(LoginComponent);

