import React, { useEffect, useContext } from 'react';
import { Button, Dialog, DialogTitle, DialogContent, TextField } from '@material-ui/core';
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { KLXR3 } from '../../services/projectService';
import { PetroContext } from '../../context/petroContext';
import './project.css';

const NewProjectDialog = (props) => {
    const { Token, dispatchAction } = useContext(PetroContext)
    const [selectedDate, setSelectedDate] = React.useState(new Date());

    const handleDateChange = date => {
      setSelectedDate(date);
    };

    useEffect(() => {
        if (props.project && props.project.date) {
            setSelectedDate(new Date(props.project.date));
        }
    }, [props.open, props.project])

    const reloadProjects = () => {
        if (Token) {
            KLXR3.BEQ1L(Token).then(resp => {
                dispatchAction({type:"SET_PROJECTS", data: resp.data})
                }, error => {
                    alert('Error: fail to refresh project list')
                })    
        }
    }

    return (
        <Dialog open={props.open} aris-labelledby="form-dialog-title" maxWidth={'sm'} fullWidth={true}>
            <DialogTitle id="form-dialog-title">{props.create ? "Create new project" : "Edit project"}</DialogTitle>
            <DialogContent>

            <Formik
                    initialValues={ !props.create ? {
                            projectName: props.project.name,
                            longitude: props.project.lng,
                            latitude: props.project.lat,
                            place: props.project.place,
                            description: props.project.description,
                        } :
                        {
                            projectName: '',
                            longitude: '',
                            latitude: '',
                            place: '',
                            description: '',
                            }
                    }
                    validationSchema={Yup.object().shape({
                        projectName: Yup.string().required('Project name is required'),
                        longitude: Yup.number().required('Longitude is required').min(-180, "Minimum value is -180").max(180, "Maximum value is 180"),
                        latitude: Yup.number().required('Latitude is required').min(-90, "Minimum value is -90").max(90, "Maximum value is 90"),
                        place: Yup.string().nullable().oneOf([null, undefined], ''),
                        description: Yup.string().nullable().oneOf([null, undefined], ''),
                    })}
                    onSubmit={({ projectName, longitude, latitude, place, description }, { setStatus, setSubmitting }) => {
                        setStatus();
                        if (props.create) {
                            let formData = new FormData();
                            formData.append("name", projectName);
                            formData.append("date", selectedDate.toLocaleDateString("en-US"));
                            formData.append("longitude", longitude);
                            formData.append("latitude", latitude);
                            formData.append("place", place);
                            formData.append("description", description);
                            // formData.append("file", selectFile);
                            
                            KLXR3.AKD5Q(formData, Token).then(res => {reloadProjects(); props.handleDialogClose()},
                            error => alert('Fail to create project'));
                        }
                        else {
                            let formData = new FormData();
                            formData.append("name", projectName);
                            formData.append("date", selectedDate.toLocaleDateString("en-US"));
                            formData.append("longitude", longitude);
                            formData.append("latitude", latitude);
                            formData.append("place", place);
                            formData.append("description", description);
                            formData.append('_method', 'PATCH');

                            KLXR3.SAXS1(props.project.id, formData, Token).then(res => reloadProjects(),
                            error => alert('Fail to update project'));
                            props.handleDialogClose();
                        }
                    }}
                    render={({ errors, status, touched, isSubmitting }) => (
                        <Form>                          
                            <div style={{display:'flex', justifyContent: 'space-between', marginBottom:'20px'}}>
                                <Field name="projectName"> 
                                   {({field, form: { touched, errors }, meta,}) => (
                                        <div style={{display:'flex', flexDirection:'column', justifyContent: 'space-between', width: '48%'}}>
                                            <TextField type="text" variant="outlined" label="Project name" fullWidth InputLabelProps={{shrink: true}}  {...field} /> 
                                            <div><ErrorMessage name="projectName" component="div" style={{color:'red'}} /></div>
                                        </div>
                                   )}
                                </Field> 
                                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                    <KeyboardDatePicker style={{margin: 0, width: '48%'}}
                                        disableToolbar
                                        variant="inline"
                                        format="MM/dd/yyyy"
                                        margin="normal"
                                        id="date-picker-inline"
                                        label="Select a date"
                                        value={selectedDate}
                                        onChange={handleDateChange}
                                        KeyboardButtonProps={{'aria-label': 'change date'}}
                                    />
                                </MuiPickersUtilsProvider>                        
                            </div>
                            {/* {props.create &&
                            <div style={{display:'flex', flexDirection:'column', justifyContent: 'space-between', marginBottom:'20px', paddingRight:'25px'}}>
                                <fieldset className="myFieldSet">
                                    <legend>Project file</legend>
                                    <input type="file" name="file" accept=".wfp" onChange={e => {setSelectFile(e.target.files[0]); setSelectFileError(false)}}></input>
                                </fieldset>
                                {selectFileError && <div style={{color: 'red'}}>
                                    <span>Project file is required</span>
                                    </div>}
                            </div>
                            } */}
                            <div style={{display:'flex', justifyContent: 'space-between', marginBottom:'20px'}}>
                                <Field name="longitude"> 
                                   {({field, form: { touched, errors }, meta,}) => (
                                        <div style={{display:'flex', flexDirection:'column', justifyContent: 'space-between', width: '48%'}}>
                                            <TextField type="text" variant="outlined" label="longitude" fullWidth InputLabelProps={{shrink: true}}  {...field} /> 
                                            <div><ErrorMessage name="longitude" component="div" style={{color:'red'}} /></div>
                                        </div>
                                   )}
                                </Field> 
                                <Field name="latitude"> 
                                   {({field, form: { touched, errors }, meta,}) => (
                                        <div style={{display:'flex', flexDirection:'column', justifyContent: 'space-between', width: '48%'}}>
                                            <TextField type="text" variant="outlined" label="latitude" fullWidth InputLabelProps={{shrink: true}}  {...field} /> 
                                            <div><ErrorMessage name="latitude" component="div" style={{color:'red'}} /></div>
                                        </div>
                                   )}
                                </Field> 
                            </div>
                            <div style={{display:'flex', justifyContent: 'space-between', marginBottom:'20px'}}>
                                <Field name="place"> 
                                   {({field, form: { touched, errors }, meta,}) => (
                                        <div style={{display:'flex', flexDirection:'column', justifyContent: 'space-between', width: '100%'}}>
                                            <TextField type="text" variant="outlined" label="place" fullWidth InputLabelProps={{shrink: true}}  {...field} /> 
                                            <div><ErrorMessage name="place" component="div" style={{color:'red'}} /></div>
                                        </div>
                                   )}
                                </Field> 
                            </div>                            
                            <div style={{display:'flex', justifyContent: 'space-between', marginBottom:'20px'}}>
                                <Field name="description"> 
                                   {({field, form: { touched, errors }, meta,}) => (
                                        <div style={{display:'flex', flexDirection:'column', justifyContent: 'space-between', width: '100%'}}>
                                            <TextField type="text" multiline rows="4" variant="outlined" label="description" fullWidth InputLabelProps={{shrink: true}}  {...field} /> 
                                            <div><ErrorMessage name="description" component="div" style={{color:'red'}} /></div>
                                        </div>
                                   )}
                                </Field> 
                            </div>                            

                            <div style={{display:'flex', justifyContent: 'flex-end'}}>
                                <Button type="submit" style={{marginRight: '40px', textTransform:'none'}} size="medium" variant="contained" color="primary" >{props.create?"Create":"Update"}</Button>
                                <Button style={{marginRight: '10px', textTransform:'none'}}  size="medium" variant="contained" onClick={() => props.handleDialogClose()} >Cancel</Button>
                            </div>

                        </Form>
                    )}
                />   

            </DialogContent>
        </Dialog>
    )
}

export default NewProjectDialog;
