import React, { useContext, useState } from "react";
import { PetroContext } from './context/petroContext';
import { Paper } from '@material-ui/core';
import { Home, ArrowDropDown, AccountCircle } from '@material-ui/icons';
import { Link, withRouter } from "react-router-dom";
import "./styles.css";
import WellFlagIcon from './wellflag.svg';

const HeaderComponent = (props) => {
    const { CurrentUser, dispatchAction } = useContext(PetroContext);
    const [ inParent, setInParent ]= useState(false);
    const [ inChild, setInChild ] = useState(false);

    return (
        <div style={{zIndex: 100, position:"fixed", top: 0, left:0, width:"100%", height: "150px", backgroundColor: "#FFFFFF", overflow:'visible'}}>
            <div style={{height: "100px", width: "80%", margin: 'auto', display:'flex', justifyContent:'space-between', alignItems:'center'}}>
                <div>
                    <img src={WellFlagIcon} width="48" height="36" alt="" />
                    <span style={{marginLeft:'10px', marginRight:"2px", color: '#C85050', fontFamily: 'Impact', fontSize: '42px'}}>Well</span>
                    <span style={{fontFamily: 'Impact', fontSize: '42px', color: '#008000'}}>Flag</span>
                    <span style={{fontFamily: 'Impact', fontSize: '30px', color: '#666666', marginLeft:'30px'}}>Innovative Web 3D Engine</span>
                </div>
                <div>
                    <span style={{fontSize: '30px', color: '#000000'}}>wellflag3d@yahoo.com</span>
                </div>
            </div>
            <div style={{height: "50px", width: "100%", backgroundColor:"#0B74A4", fontFamily: "Helvetica, Helvetica, Arial, sans-serif", }}>
                <div style={{height: "50px", width: "80%", margin:'auto', display:'flex',flexDirection:'row', justifyContent:'space-between', alignItems:'flex-start', paddingTop:'2px'}}>

                    <div style={{display:'flex', flexDirection:'row'}}>
                        <Link style={{textDecoration:'none', marginRight:'10px'}} to="/">
                            <div className="topMenu">
                                <Home fontsize="large" />
                                <div style={{marginLeft:'4px', marginTop:'5px'}}>Home</div>
                            </div>
                        </Link>
                    </div>           

                    {CurrentUser ? 
                    <div style={{display:'flex', flexDirection:'row', justifyContent:'flex-start'}}>
                         <div style={{display:'flex', flexDirection:'column', justifyContent:'flex-start',
                           alignItems:'flex-end'}}>
                               <div style={{display:'flex', flexDirection:'row', justifyContent:'flex-start'}}>
                                <Link style={{textDecoration:'none', backgroundColor:"#FFFFFF00"}} to="/projects">
                                        <div className="topMenu">
                                            <div style={{margin: 'auto'}}>My Projects</div>
                                        </div>
                                    </Link>
                                <div onMouseEnter={ (e) => { setInParent(true) }} onMouseLeave={ (e) => { setInParent(false) }} 
                                        className="topMenu" 
                                        style={{ color: inParent ? '#0B74A4' : 'white', display:'flex', flexDirection:'row', 
                                        justifyContent:'center'}}>
                                        <AccountCircle fontSize="large" />
                                        <ArrowDropDown fontsize="large" />
                                </div>
                                </div>
                               <div onMouseEnter={ (e) => { setInChild(true) }} onMouseLeave={ (e) => { setInChild(false) }} style={{paddingTop:'4px'}}>
                                    { (inParent || inChild ) &&
                                    <Paper style={{backgroundColor:'#0B74A4'}}>
                                        <Link style={{textDecoration:'none', textAlign:'left', borderRadius:'4px'}} to="/myaccount">
                                            <div className="dropMenu">
                                                <div>My account</div>
                                            </div>
                                        </Link>
                                        { (CurrentUser && CurrentUser.role === "SuperUser") &&
                                        <Link style={{textDecoration:'none', textAlign:'left', borderRadius:'4px'}} to="/myviewers">
                                            <div className="dropMenu">
                                                <div>My project viewers</div>
                                            </div>
                                        </Link> 
                                        }
                                        <Link style={{textDecoration:'none'}} to="/changepassword">
                                            <div className="dropMenu">
                                                <div>Change password</div>
                                            </div>
                                        </Link>   
                                        <div className="dropMenu" onClick={() => {
                                            props.history.push('/');
                                            dispatchAction({type:'USER_LOGOUT'});
                                            localStorage.removeItem('loginData')
                                        }}>
                                            <div>Sign Out</div>
                                        </div>                                  
                                    </Paper>
                                    }
                               </div>
                         </div>

                    </div>
                    :
                    <div style={{display:'flex', flexDirection:'row'}}>
                        <Link style={{textDecoration:'none'}} to="/signin">
                            <div className="topMenu">
                                <div style={{margin: 'auto'}}>Sign In / Register</div>
                            </div>
                        </Link>
                    </div>
                    }
                </div>
            </div>
        </div>
    );
}

export default withRouter(HeaderComponent);

