import React, { Fragment, useContext, useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { MPTL2 } from '../services/userService';
import { withRouter } from "react-router-dom";
import { PetroContext } from '../context/petroContext';
import { Button } from '@material-ui/core';
import HeaderComponent from '../header';
import FooterComponent from '../footer';
import "../styles.css";

const useStyles = makeStyles({
    root: {
      boxShadow: '0 0 6px 6px rgba(200, 200, 200, .3)',
      width:"400px", 
      margin:"auto", 
      padding: "20px"
    },
    title: {
        fontSize: '30px',
        fontWeight:'bold',
        marginTop: '20px',
        marginBottom:"30px",
    },
    label: {
        marginTop: '20px',
        fontSize: '20px',
    },
    input: {
        fontSize: '16px',
        width: '100%',
        padding: '6px',
        marginTop: '10px',
        marginBottom:'10px'
    },
    sumbit: {
        marginTop: '20px',
        textAlign:'center',
    },
    error: {
        color: 'red'
    },
    responseError: {
        textAlign: 'left',
        padding:'10px',
        backgroundColor: '#F8D7DA',
        color: 'black'
    }

  });


const ChangePasswordComponent = (props) => {
    const classes = useStyles();
    const [errorMessage, setErrorMessage] = useState(null)
    const { Token } = useContext(PetroContext);

    return (
      <Fragment>
        <HeaderComponent />
        <div style={{paddingTop: '170px', paddingLeft:"10%", paddingRight:"10%", paddingBottom:'20px' }}>
            <div className={classes.root} >
            <div className={classes.title} >Change Password</div>
                <Formik
                    initialValues={{
                        password: '',
                        newpassword: '',
                        confirmPassword: '',
                    }}
                    validationSchema={Yup.object().shape({
                        password: Yup.string().required("Current password is required").min(3, 'Minimum 6 characters').max(30, 'Maximum 30 characters'),
                        newpassword: Yup.string().required('New password is required').min(3, 'Minimum 6 characters').max(30, 'Maximum 30 characters')
                                .oneOf([Yup.ref('confirmPassword'), null], 'Passwords must match'),
                        confirmPassword: Yup.string().required('Confirm new password is required').min(6, 'Minimum 6 characters').max(30, 'Maximum 30 characters')
                                .oneOf([Yup.ref('newpassword'), null], 'Passwords must match')
                    })}
                    onSubmit={({ password, newpassword, confirmPassword }, { setStatus, setSubmitting }) => {
                        setStatus();
                        let formData = new FormData();
                        formData.append("password", password);
                        formData.append("newpassword", newpassword);

                        MPTL2.Q0Q9C(password, newpassword, Token).then(res => {
                            setErrorMessage(null)
                        }, err => setErrorMessage(err.response.data.message))
                    }}
                    render={({ errors, status, touched, isSubmitting }) => (
                        <Form>                          
                            <div>
                                <span className={classes.label}>Current password</span>
                            </div>
                            <div>
                                <Field name="password" type="text" className={classes.input} />
                            </div>
                            <div>
                                <ErrorMessage name="password" component="div" className={classes.error} />
                            </div>

                            <div style={{marginTop:'20px'}}>
                                <span className={classes.label}>New password</span>
                            </div>
                            <div>
                                <Field name="newpassword" type="password" className={classes.input} />
                            </div>
                            <div>
                                <ErrorMessage name="newpassword" component="div" className={classes.error} />
                            </div>

                            <div style={{marginTop:'20px'}}>
                                <span className={classes.label}>Confirm new password</span>
                            </div>
                            <div>
                                <Field name="confirmPassword" type="password" className={classes.input} />
                            </div>
                            <div>
                                <ErrorMessage name="confirmPassword" component="div" className={classes.error} />
                            </div>
                            <div className={classes.sumbit}>
                                <Button type="submit" variant="contained" color="primary" style={{width:'100%', textTransform:'capitalize', backgroundColor:'#0B74A4', color:'#FFFFFF'}}>
                                    Change Password
                                </Button>
                            </div>
                            <div className={classes.sumbit}>
                                {errorMessage && <div className={classes.responseError}>{errorMessage}</div>}
                            </div>
                        </Form>
                    )}
                />                

            </div>
        </div>
        <FooterComponent />
      </Fragment>   
    );

}

export default withRouter(ChangePasswordComponent);

