import React, { useContext, useState, useEffect }  from 'react';
import { PetroContext } from '../../context/petroContext';
import { Menu, MenuItem } from '@material-ui/core';
import { Redirect, withRouter } from "react-router-dom";

import rightIcon from './right.svg';
import downIcon from './down.svg';

import projectIcon from './project.svg';
import wellIcon from './wellbore.svg';
import perfIcon from './perf.svg';
import geophoneIcon from './geophone.svg';
import lasIcon from './las.svg';
import fractureIcon from './fracture.svg';
import eventIcon from './event.svg';
import checkIcon from './checked.svg';

import './treeView.css';
import AddWellborDialog from '../dialog/addWellboreDlg';
import AddPerforationDialog from '../dialog/addPerforationDlg';
import AddGeophoneDialog from '../dialog/addGeophoneDlg';
import AddLasDialog from '../dialog/addLasDlg';
import AddMSEventsDialog from '../dialog/addMSEventsDlg';
import AddFractureDialog from '../dialog/addFractureDlg';
import ChangeLasDialog from '../dialog/changeLasDlg';
import ConfirmDialog from '../../components/dialogs/confirmDialog';
import { KLXR3 } from '../../services/projectService';

const TreeView = (props) => {
    const { Token, BK7BT, dispatchAction, FCYDC } = useContext(PetroContext)
    const [ activeTreeItems, setActiveTreeItems] = useState();
    const [ selectedItem, setSelectedItem] = useState(null);
    const [ deleteInfo, setDeleteInfo ] = useState(null);

    const [projectAnchorEl, setProjectAnchorEl] = React.useState(null);
    const [wellGroupAnchorEl, setWellGroupAnchorEl] = React.useState(null);
    const [eventGroupAnchorEl, setEventGroupAnchorEl] = React.useState(null);
    const [wellAnchorEl, setWellAnchorEl] = React.useState(null);
    const [perfAnchorEl, setPerfAnchorEl] = React.useState(null);
    const [geophoneAnchorEl, setGeophoneAnchorEl] = React.useState(null);
    const [fractureAnchorEl, setFractureAnchorEl] = React.useState(null);
    const [lasAnchorEl, setLasAnchorEl] = React.useState(null);
  
    const [addWellboreDlgOpen, setAddWellboreDlgOpen] = React.useState(false);
    const [addPerforationDlgOpen, setAddPerforationDlgOpen] = React.useState(false);
    const [addGeophoneDlgOpen, setAddGeophoneDlgOpen] = React.useState(false);
    const [addLasDlgOpen, setAddLasDlgOpen] = React.useState(false);
    const [addMSEventsDlgOpen, setAddMSEventsDlgOpen] = React.useState(false);
    const [addFractureDlgOpen, setAddFractureDlgOpen] = React.useState(false);
    const [changeLasDlgOpen, setChangeLasDlgOpen] = React.useState(false);

    const onChecked = (id) => {
        let item = BK7BT.find(a => a.PKF0U === id);
        if (item !== null) {
            item.C3NQE = !item.C3NQE;
            let activeItems = getActiveItem(BK7BT)
            setActiveTreeItems(activeItems); 

            let FYLX0 = [];
            for (var i = 0; i < BK7BT.length; i++) {
                if (BK7BT[i].C3NQE) {
                FYLX0.push(BK7BT[i].PKF0U);
                }
            }

            dispatchAction({type: 'SET_TREEVIEW_VISIBLE', data: FYLX0})
        }
    }

    const onCollapse = (id) => {
        let item = BK7BT.find(a => a.PKF0U === id);
        if (item !== null) {
            item.FFFF9 = !item.FFFF9;
            let activeItems = getActiveItem(BK7BT)
            setActiveTreeItems(activeItems);
        }
    }
    
    const onOpenContextMenuMenu = (event, item) => {
        if (item.VVYQ9 === 0) { setProjectAnchorEl(event.currentTarget); }             // project
        else if (item.VVYQ9 === 1) { setWellGroupAnchorEl(event.currentTarget); }      // Wellgroup 
        else if (item.VVYQ9 === 2) { setWellAnchorEl(event.currentTarget); }           // Wellbore 
        else if (item.VVYQ9 === 3) { setEventGroupAnchorEl(event.currentTarget); }     // Event Group
        else if (item.VVYQ9 === 4) { setEventGroupAnchorEl(event.currentTarget); }     // Event well
        else if (item.VVYQ9 === 5) { alert(item.VVYQ9 + ' Event') }                      // Event
        else if (item.VVYQ9 === 6) { setGeophoneAnchorEl(event.currentTarget);  }      // Geophone
        else if (item.VVYQ9 === 7) { setPerfAnchorEl(event.currentTarget); }           // Perf
        else if (item.VVYQ9 === 8) { setLasAnchorEl(event.currentTarget); }                        // Las
        else if (item.VVYQ9 === 9) { setFractureAnchorEl(event.currentTarget); }              // Plane Group 
        else if (item.VVYQ9 === 10) { setFractureAnchorEl(event.currentTarget); }                  // Plane
    }

    const onDeleteItem = () => {
        if (deleteInfo === null || selectedItem === null) return;

        if (deleteInfo.type === "wellbore") {
            KLXR3.EW2SH(FCYDC, selectedItem.PKF0U, Token)
            .then(res => { reloadWindow(); }, err => { alert("Fail to remove wellbore!")});
        }
        else if (deleteInfo.type === "perf") {
            KLXR3.I9RB0(FCYDC, selectedItem.PKF0U, Token)
            .then(res => { reloadWindow(); }, err => { alert("Fail to remove perf!")});
        }
        else if (deleteInfo.type === "geophone") {
            KLXR3.FEV0Q(FCYDC, selectedItem.PKF0U, Token)
            .then(res => { reloadWindow(); }, err => { alert("Fail to remove geophone!")});
        }
        else if (deleteInfo.type === "events") {
            KLXR3.N1LT3(FCYDC, Token)
            .then(res => { reloadWindow(); }, err => { alert("Fail to remove events!")});   
        }
        else if (deleteInfo.type === "fracture") {
            KLXR3.LRGLX(FCYDC, Token)
            .then(res => { reloadWindow(); }, err => { alert("Fail to remove fracture!")});   
        }
        else if (deleteInfo.type === "las") {
            KLXR3.S56YJ(FCYDC, selectedItem.PKF0U, Token)
            .then(res => { reloadWindow(); }, err => { alert("Fail to remove las!")}); 
        }
    }

    const getActiveItem = (items) => {
        if (items === null || items.length < 1) return [];

        let currentDepth = 0;
        let activeItems = [];

        for (var i = 0; i < items.length; i++) {
            if (currentDepth === 0) {
                activeItems.push(items[i]);
                if (!items[i].FFFF9) {
                    currentDepth = items[i].VN6NV;
                }
            }
            else if (items[i].VN6NV <= currentDepth) {
                activeItems.push(items[i]);
                if (!items[i].FFFF9) {
                    currentDepth = items[i].VN6NV;
                }
                else {
                    currentDepth = 0;
                }
            }
        }
        return activeItems;
    }

    const getMenuContent = (item) => {
        let typeIcon = undefined;
        if (item.VVYQ9 === 0) { typeIcon = projectIcon }
        else if (item.VVYQ9 === 1 || item.VVYQ9 === 2) { typeIcon = wellIcon }
        else if (item.VVYQ9 === 3 || item.VVYQ9 === 4 || item.VVYQ9 === 5) { typeIcon = eventIcon }
        else if (item.VVYQ9 === 6) { typeIcon = geophoneIcon }
        else if (item.VVYQ9 === 7) { typeIcon = perfIcon }
        else if (item.VVYQ9 === 8) { typeIcon = lasIcon }
        else if (item.VVYQ9 === 9 || item.VVYQ9 === 10) { typeIcon = fractureIcon }

        return (
            <div className="treeContent" aria-controls="projectMenu" aria-haspopup="true" onClick={e => {
                setSelectedItem(item); onOpenContextMenuMenu(e, item)
            }}>
                <div className="treeIconDiv"><img className="treeIcon" src={typeIcon} alt="icon" /></div>
                <span style={{whiteSpace:"nowrap"}}>{item.EOWLF}</span>
            </div>
        );
    }    

    const getItemContent = (item, index) => {
        let collapsedIcon = item.FFFF9 ? rightIcon : downIcon;
        let collapsedItem = item.VVYQ9 !== 0 ? <div className="treeArrow" onClick={() => onCollapse(item.PKF0U)}>
                            <img className="treeIcon" src={collapsedIcon} alt="collapsed Icon" />
                            </div> : '';
        let checkButton = <div className="treeCheckDiv" onClick={() => {onChecked(item.PKF0U)}}>
                {item.C3NQE ? <img className="treeIcon" src={checkIcon} alt="icon" /> : <img className="treeIcon" src={''} alt="" />}
            </div>

        let checkItem = item.VN6NV !== 0 ? checkButton : '';

        return <div key={index} style={{overflow:'hidden'}}>
                   <div className="treeRow">
                        {getEmptySpaces(item)} 
                        {item.CSYC6 && collapsedItem } 
                        {checkItem} 
                        {getMenuContent(item)} 
                    </div>
                </div> 
    }

    const reloadWindow = () => {
        window.location.reload(true);
    };

    const getEmptySpaces = (item) => {
        let levelArray = Array(item.VN6NV).fill(0);

        if (item.CSYC6) 
            return levelArray.map(a => <span key={a}>&nbsp;&nbsp;&nbsp;&nbsp;</span>)   
        else
            return levelArray.map(a => <span key={a}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>)   
     }

    useEffect(() => {
        if (BK7BT !== undefined && BK7BT !== null) {
            let activeItems = getActiveItem(BK7BT);
            setActiveTreeItems(activeItems);
        }
    }, [BK7BT])

    return (
        ( activeTreeItems !== undefined && activeTreeItems !== null) ? 
        <div>
        {activeTreeItems.map((item, index) => getItemContent(item, index))}

        <Menu
            id="projectMenu"
            anchorEl={projectAnchorEl}
            getContentAnchorEl={null}
            anchorOrigin={{ vertical: "bottom" }}
            transformOrigin={{ vertical: "top" }}
            keepMounted
            open={Boolean(projectAnchorEl)}
            onClose={() => setProjectAnchorEl(null)}
            >
            <MenuItem onClick={()=> {setProjectAnchorEl(null); setAddWellboreDlgOpen(true); }}>Add Wellbore</MenuItem>
            <MenuItem onClick={() => {setProjectAnchorEl(null); setAddMSEventsDlgOpen(true);}}>Add MicroSeismic Events</MenuItem>
            <MenuItem onClick={() => {setProjectAnchorEl(null); setAddFractureDlgOpen(true);}}>Add Fracture Data</MenuItem>
        </Menu> 

        <Menu
            id="eventGroupMenu"
            anchorEl={eventGroupAnchorEl}
            getContentAnchorEl={null}
            anchorOrigin={{ vertical: "bottom" }}
            transformOrigin={{ vertical: "top" }}
            keepMounted
            open={Boolean(eventGroupAnchorEl)}
            onClose={() => setEventGroupAnchorEl(null)}
            >
            <MenuItem onClick={() => {setEventGroupAnchorEl(null); setAddMSEventsDlgOpen(true);}}>Add MicroSeismic Events</MenuItem>
            <MenuItem onClick={() => {setDeleteInfo({open: true, type: "events", content: 'all events'}); setEventGroupAnchorEl(null)}}>Remove MicroSeismic Events</MenuItem>
        </Menu> 

        <Menu
            id="wellGroupMenu"
            anchorEl={wellGroupAnchorEl}
            getContentAnchorEl={null}
            anchorOrigin={{ vertical: "bottom" }}
            transformOrigin={{ vertical: "top" }}
            keepMounted
            open={Boolean(wellGroupAnchorEl)}
            onClose={() => setWellGroupAnchorEl(null)}
            >
            <MenuItem onClick={()=> {setWellGroupAnchorEl(null); setAddWellboreDlgOpen(true); }}>Add Wellbore</MenuItem>
        </Menu> 

        <Menu
            id="wellMenu"
            anchorEl={wellAnchorEl}
            getContentAnchorEl={null}
            anchorOrigin={{ vertical: "bottom" }}
            transformOrigin={{ vertical: "top" }}
            keepMounted
            open={Boolean(wellAnchorEl)}
            onClose={() => setWellAnchorEl(null)}
            >
            <MenuItem onClick={() => {setWellAnchorEl(null); setAddLasDlgOpen(true); }}>Add Las</MenuItem>
            <MenuItem onClick={() => {setWellAnchorEl(null); setAddPerforationDlgOpen(true); }}>Add Perf</MenuItem>
            <MenuItem onClick={() => {setWellAnchorEl(null); setAddGeophoneDlgOpen(true); }}>Add Geophone</MenuItem>
            <MenuItem onClick={() => {setDeleteInfo({open: true, type: "wellbore", content: selectedItem.EOWLF}); setWellAnchorEl(null)}}>Remove this wellbore</MenuItem>
        </Menu> 

        <Menu
            id="perfMenu"
            anchorEl={perfAnchorEl}
            getContentAnchorEl={null}
            anchorOrigin={{ vertical: "bottom" }}
            transformOrigin={{ vertical: "top" }}
            keepMounted
            open={Boolean(perfAnchorEl)}
            onClose={() => setPerfAnchorEl(null)}
            >
            <MenuItem onClick={() => {setDeleteInfo({open: true, type: "perf", content: selectedItem.EOWLF}); setPerfAnchorEl(null)}}>Remove this perf</MenuItem>
        </Menu>         

        <Menu
            id="geophoneMenu"
            anchorEl={geophoneAnchorEl}
            getContentAnchorEl={null}
            anchorOrigin={{ vertical: "bottom" }}
            transformOrigin={{ vertical: "top" }}
            keepMounted
            open={Boolean(geophoneAnchorEl)}
            onClose={() => setGeophoneAnchorEl(null)}
            >
            <MenuItem onClick={() => {setDeleteInfo({open: true, type: "geophone", content: selectedItem.EOWLF}); setGeophoneAnchorEl(null)}}>Remove this geophone</MenuItem>
        </Menu>    

        <Menu
            id="lasMenu"
            anchorEl={lasAnchorEl}
            getContentAnchorEl={null}
            anchorOrigin={{ vertical: "bottom" }}
            transformOrigin={{ vertical: "top" }}
            keepMounted
            open={Boolean(lasAnchorEl)}
            onClose={() => setLasAnchorEl(null)}
            >
            <MenuItem onClick={() => {setLasAnchorEl(null); setChangeLasDlgOpen(true);}}>Change las channel</MenuItem>
            <MenuItem onClick={() => {setDeleteInfo({open: true, type: "las", content: selectedItem.EOWLF}); setLasAnchorEl(null)}}>Remove this las</MenuItem>
        </Menu> 

        <Menu
            id="fractureMenu"
            anchorEl={fractureAnchorEl}
            getContentAnchorEl={null}
            anchorOrigin={{ vertical: "bottom" }}
            transformOrigin={{ vertical: "top" }}
            keepMounted
            open={Boolean(fractureAnchorEl)}
            onClose={() => setFractureAnchorEl(null)}
            >
            <MenuItem onClick={() => {setDeleteInfo({open: true, type: "fracture", content: selectedItem.EOWLF}); setFractureAnchorEl(null)}}>Remove fracture data</MenuItem>
        </Menu>               

        <AddWellborDialog open={addWellboreDlgOpen} handleDialogClose={() => {setAddWellboreDlgOpen(false); }} />
        <AddPerforationDialog open={addPerforationDlgOpen} projectId={FCYDC} item={selectedItem} handleDialogClose={() => {setAddPerforationDlgOpen(false); }} />
        <AddGeophoneDialog open={addGeophoneDlgOpen} projectId={FCYDC} item={selectedItem} handleDialogClose={() => {setAddGeophoneDlgOpen(false); }} />
        <AddLasDialog open={addLasDlgOpen} projectId={FCYDC} item={selectedItem} handleDialogClose={() => {setAddLasDlgOpen(false); }} />
        <AddMSEventsDialog open={addMSEventsDlgOpen} projectId={FCYDC} item={selectedItem} handleDialogClose={() => {setAddMSEventsDlgOpen(false); }} />
        <AddFractureDialog open={addFractureDlgOpen} projectId={FCYDC} item={selectedItem} handleDialogClose={() => {setAddFractureDlgOpen(false); }} />
        <ChangeLasDialog open={changeLasDlgOpen} projectId={FCYDC} item={selectedItem} handleDialogClose={() => {setChangeLasDlgOpen(false); }} />

        { deleteInfo &&
        <ConfirmDialog open={deleteInfo.open} title="Delete confirmation" content={"Are you sure to delete " + deleteInfo.type + ": " + deleteInfo.content + "?"} 
          handleConfirmAction={onDeleteItem} handleConfirmCancel={() => {setDeleteInfo(null)}} />
        }

        </div> : <div>No data</div>
    )
    
}

export default withRouter(TreeView);
