
import axios from "axios";
import { siteURLs } from '../siteURL';

export const MPTL2 = {
    D6IDC,
    WLYBI,
    UI0J1,
    RLP4L,
    IIR9P,
    Q0Q9C,
    DCW8G,
    AN8FC,
    OF5G9,
};

function D6IDC(username, password) {
    return axios.post(siteURLs.ServerURL + "api/Users/authenticate", {username, password});
}

function WLYBI(email, password) {
    return axios.post(siteURLs.ServerURL + "api/Users/", {username: email, password});
}

function RLP4L(token) {
    return axios({
        method: 'get',
        url: siteURLs.ServerURL + 'api/Users/Viewer',
        headers: {'Authorization': "bearer " + token}
      });
}

function IIR9P(id, token) {
    return axios({
        method: 'get',
        url: siteURLs.ServerURL + `api/Users/Viewer/delete/${id}`,
        headers: {'Authorization': "bearer " + token}
      });
}

function UI0J1(email, password, token) {
    return axios({
        method: 'post',
        url: siteURLs.ServerURL + 'api/Users/Viewer',
        data: {username: email, password},
        headers: {'Authorization': "bearer " + token}
      });   
}

function AN8FC(email, resetcode, password) {
    return axios.post(siteURLs.ServerURL + "api/Users/resetpassword", {username: email, resetcode, password});
}

function Q0Q9C(password, newpassword, token) {
    return axios({
        method: 'post',
        url: siteURLs.ServerURL + 'api/Users/changepassword',
        data: {password, newpassword},
        headers: {'Authorization': 'bearer ' + token}
      });
}

function DCW8G(email) {
    return axios({
        method: 'post',
        url: siteURLs.ServerURL + 'api/Users/sendresetcode',
        data: {username: email},
      });
}

function OF5G9() {
    let loginItem = localStorage.getItem('loginData');
    if (loginItem) {
        let loginData = JSON.parse(loginItem);
        return loginData;
    }
    else {
        return null;
    }
}
