import React, { useContext, Fragment } from "react";
import { B91UN } from './scene';
import * as vec3 from './glMatrix/vec3';
import { OASCO, A3BON } from "./sceneConsts";
import { PetroContext } from '../context/petroContext';
import checkIcon from "./checkWhite.svg";

class SceneComponent extends React.Component {
  static contextType = PetroContext;

  constructor(props) {
    super(props);

    this.I7L2Q = false;
    this.KKRCG = null;
    this.NE4GU = false;
    this.A8G6R = 0;
    this.XVK1F = null;

    this.state = { isMouseDown: -1, delta: 0, x: 0, y: 0, xDown: 0, yDown: 0, xUp: 0, yUp: 0,
                   AJ8AT : 0, QFOGU: undefined,
                   clientWidth: 0, clientHeight: 0, showInfo: true, unit: 'foot' };

    this.RPL5I = this.RPL5I.bind(this);
    this.YVW6B = this.YVW6B.bind(this);
    this.QTAV9 = this.QTAV9.bind(this);
    this.B78WB = this.B78WB.bind(this);
    this.CRY9N = this.CRY9N.bind(this);
    this.onUnitChange = this.onUnitChange.bind(this);
  }

  OJ3DJ() {
    const { dispatchAction, DTICP } = this.context;

    if (!this.I7L2Q) {
      this.S58HW = this.refs.NQJD8.getContext("webgl");
      if (!this.S58HW) {
        alert(
          "Unable to initialize WebGL. Your browser or machine may not support it."
        );
        return;
      }
  
      this.UM55Y = new B91UN();
      this.UM55Y.GBNM5(this.S58HW, this.AU16L);

      this.XVK1F = new A3BON();
      this.XVK1F.LT_init(this.UM55Y.BG9YH.X, 
                                           this.UM55Y.BG9YH.Y,
                                           this.UM55Y.BG9YH.Z)

      dispatchAction({type:'SET_GEOLOCATION', data: this.UM55Y.BG9YH});
      dispatchAction({type:'SET_TREEVIEW', data: this.UM55Y.JSPUH});
      dispatchAction({type:'SET_EVENTDATA', data: this.AU16L.I6EJC});
      if (this.AU16L.BDF0D !== null) {
        dispatchAction({type:'SET_FRACTUREDATA', data: this.AU16L.BDF0D.KYEND});
      }
      
      this.I7L2Q = true;      
    }

    if (this.I7L2Q && DTICP !== null && DTICP !== undefined) {
        let width = DTICP[0];
        let height = DTICP[1];

        var QXOBO = document.getElementById('NQJD8');
        QXOBO.width = width;
        QXOBO.height = height-4;

        if (this.state.showInfo) {
          var M9GR7 = document.getElementById('W7KD6');
          M9GR7.style.height = height-4;  
        }

        this.KKRCG = this.UM55Y.pickObject(width, height-4, this.state.x, this.state.y);
        this.setState({ AJ8AT : this.KKRCG.X5P6V,
                       QFOGU: this.KKRCG.NAEJL});
        
        if (this.KKRCG.NAEJL !== null && this.KKRCG.NAEJL !== undefined) {
          let output = [];
          this.KKRCG.MI3A0.forEach(m => {
            if (m.name === "MD" || m.name === "MD top" || m.name === "MD bottom" ||
                m.name === "East" || m.name === "North" || m.name === "Depth" || m.name === "Altitude") {
                  if (this.state.unit === 'meter') {
                    output.push({name: m.name, value: m.value + ' m'});
                  }
                  else {
                    output.push({name: m.name, value: (m.value * 3.2808399).toFixed(2) + ' ft'});
                  }
                }
            else {
              output.push({name: m.name, value: m.value});
            }
          })
          if (this.XVK1F !== null &&
            this.KKRCG.D099K !== null && this.KKRCG.D099K !== undefined) {
            let geoCoordinates = this.XVK1F.LT_LTP2LLA(this.KKRCG.D099K[0], 
              -this.KKRCG.D099K[2], 
              this.KKRCG.D099K[1]);
              output.push({name: "Geo coordinate", value: ''});
              output.push({name: "Longitude", value: geoCoordinates[0].toFixed(8)});
              output.push({name: "Latitude", value: geoCoordinates[1].toFixed(8)});
              output.push({name: "Longitude", value: geoCoordinates[3]});
              output.push({name: "Latitude", value: geoCoordinates[4]});
              if (this.state.unit === 'meter') {
                output.push({name: "Altitude", value: geoCoordinates[2].toFixed(2) + ' m'});
              }
              else {
                output.push({name: "Altitude", value: (geoCoordinates[2] * 3.2808399).toFixed(2) + ' ft'});
              }
            }
          dispatchAction({type:'SET_OBJECTINFO_DATA', data: output})
        }
        else {
          dispatchAction({type:'SET_OBJECTINFO_DATA', data: []})
        }

        if (this.NE4GU) {
          let FUKGS = new Date().getTime();
          if ((FUKGS - this.A8G6R) >= 1000) {
              this.NE4GU = false;
              this.UM55Y.RF3N4.DOAXT(1.0);
          }
          else {
            this.UM55Y.RF3N4.DOAXT((FUKGS - this.A8G6R) / 1000);
          }
        } 
        else if (this.state.isMouseDown === 1) {
          if (this.KKRCG.VUGJ9 === OASCO.G0TII) {
            this.UM55Y.RF3N4.KMA6D(1);
          }
          else if (this.KKRCG.VUGJ9 === OASCO.HYW3Y) {
            this.UM55Y.RF3N4.KMA6D(-1);
          }  
          else if (this.KKRCG.VUGJ9 === OASCO.P5S0N) {
            this.UM55Y.RF3N4.VNSDS(-2, 0);
          }
          else if (this.KKRCG.VUGJ9 === OASCO.HUDWF) {
            this.UM55Y.RF3N4.VNSDS(2, 0);
          }
          else if (this.KKRCG.VUGJ9 === OASCO.NX9V9) {
            this.UM55Y.RF3N4.VNSDS(0, 2);
          }
          else if (this.KKRCG.VUGJ9 === OASCO.XAD2I) {
            this.UM55Y.RF3N4.VNSDS(0, -2);
          }
        }     

        let { X9PG3, FBAYS, YWRJY, H1991, TA8NY } = this.context;

        this.UM55Y.OT1BU.DNTQU(X9PG3, FBAYS);
        this.UM55Y.OT1BU.G4EMK(YWRJY);
        this.UM55Y.OT1BU.Y6M0T(H1991);
        this.UM55Y.OT1BU.OM7GX(TA8NY);
        this.UM55Y.KCBLT(width, height-4, this.state.unit === 'meter');      
    }
  }  

  BSCBO(S0AVO) {
    var M9GR7 = "";    
    var MHRQA = new Buffer(S0AVO);
    for (var i = 0; i < MHRQA.length; i++) {
      var A235R = MHRQA[i];
      if (A235R > 32 && A235R < 127) {
        M9GR7 += String.fromCharCode(159 - A235R);
      }
      else {
        M9GR7 += String.fromCharCode(A235R);
      }
    }

    return JSON.parse(M9GR7);
  }
  
  componentDidUpdate(prevProps) {
    if (this.props.AU16L === prevProps.AU16L ) return;

    this.AU16L = this.BSCBO(this.props.AU16L);
    this.OJ3DJ();

    this.timer = setInterval(() => {
      this.OJ3DJ();
    }, 50);      
  }

  CRY9N(evt) {
    if (this.KKRCG !== null && this.KKRCG.X5P6V > 0 && this.KKRCG.D099K !== null && this.KKRCG.D099K !== undefined) {
      let QX8NX = vec3.create();
      vec3.sub(QX8NX, this.KKRCG.D099K, this.UM55Y.RF3N4.EUY4N);

      var TG2NI = vec3.length(QX8NX) / 2.0;

      vec3.normalize(QX8NX, QX8NX);
      vec3.scale(QX8NX, QX8NX, TG2NI);
      vec3.add(QX8NX, QX8NX, this.UM55Y.RF3N4.EUY4N);

      this.A8G6R = new Date().getTime();
      this.UM55Y.RF3N4.NR27F(QX8NX, this.KKRCG.D099K);
      this.NE4GU = true;      
    }
  }

  RPL5I(evt) {
    this.setState({ delta: evt.deltaY });
    this.UM55Y.RF3N4.KMA6D(evt.deltaY);
  }

  QTAV9(evt) {
    let AJUHG = document.getElementById('TVQ4L').getBoundingClientRect();
    let LEM57 = evt.clientX - AJUHG.left;
    let UM33R = evt.clientY - AJUHG.top;
    
    if (this.state.isMouseDown === 1) {
      this.UM55Y.RF3N4.VNSDS(LEM57 - this.state.x, UM33R - this.state.y);
    }
    this.setState({ x: LEM57, y: UM33R });
  }

  YVW6B(evt) {
    let AJUHG = document.getElementById('TVQ4L').getBoundingClientRect();
    let LEM57 = evt.clientX - AJUHG.left;
    let UM33R = evt.clientY - AJUHG.top;

    this.setState({ isMouseDown: 1, xDown: LEM57, yDown: UM33R, x: LEM57, y: UM33R });

    if (this.KKRCG !== null) {
      if (this.KKRCG.VUGJ9 != null) {
        if (this.KKRCG.VUGJ9 === OASCO.SN1XC) {
          this.UM55Y.RF3N4.O9T2J(0);
          this.A8G6R = new Date().getTime();
          this.NE4GU = true; 
        }
        else if (this.KKRCG.VUGJ9 === OASCO.LKDFL) {
          this.UM55Y.RF3N4.O9T2J(1);
          this.A8G6R = new Date().getTime();
          this.NE4GU = true; 
        }
        else if (this.KKRCG.VUGJ9 === OASCO.WO9JC) {
          this.UM55Y.RF3N4.O9T2J(2);
          this.A8G6R = new Date().getTime();
          this.NE4GU = true; 
        }
        else if (this.KKRCG.VUGJ9 === OASCO.D98EY) {
          this.UM55Y.RF3N4.O9T2J(3);
          this.A8G6R = new Date().getTime();
          this.NE4GU = true; 
        }
        else if (this.KKRCG.VUGJ9 === OASCO.TWVWB) {
          this.UM55Y.RF3N4.O9T2J(4);
          this.A8G6R = new Date().getTime();
          this.NE4GU = true; 
        }
        else if (this.KKRCG.VUGJ9 === OASCO.IAXIK) {
          this.UM55Y.RF3N4.BPQI1();
          this.A8G6R = new Date().getTime();
          this.NE4GU = true; 
        }
      }
      else if (this.KKRCG.NAEJL !== null && this.KKRCG.RSWSG === true ) {
        this.KKRCG.NAEJL.RKNNT();
      }
    }
  }

  B78WB(evt) {
    let AJUHG = document.getElementById('TVQ4L').getBoundingClientRect();
    let LEM57 = evt.clientX - AJUHG.left;
    let UM33R = evt.clientY - AJUHG.top;

    this.setState({ isMouseDown: -1, xUp: LEM57, yUp: UM33R });
  }

  onUnitChange(evt) {
    this.setState({unit: evt.target.value});
    this.context.dispatchAction({type:'SET_UNIT', data:evt.target.value})
  }

  render() {
    const {AFV32, YIUIM, LDLPU } = this.context;     

    return (
      <div id="TVQ4L" style={{background:'#DDDDDD', height:'100%', position:'relative'}} >
        <canvas id="NQJD8" ref="NQJD8"
          onDoubleClick={this.CRY9N}
          onWheel={this.RPL5I}
          onMouseMove={this.QTAV9}
          onMouseDown={this.YVW6B}
          onMouseUp={this.B78WB}         
        />

        { this.state.showInfo && 
        <div id="W7KD6" ref="W7KD6" style={{position:'absolute', top:'0px', left: '0px', color:'white', width:'200px', backgroundColor:'#44444488'}} 
            onWheel={this.RPL5I} onMouseMove={this.QTAV9} onMouseDown={this.YVW6B} onMouseUp={this.B78WB} >
            <div style={{paddingTop:'30px'}}>
            { LDLPU !== null &&
                LDLPU.map((item) => {
                  return <Fragment>{item.value === '' ? 
                    <div style={{backgroundColor:'#0000FF88'}}>{item.name}</div> : 
                    <div>{item.name} {item.value}</div>
                  }</Fragment>
                })
            }
            </div>
        </div>
        }

        <div style={{position:'absolute', top:'0px', left: '0px', color:'white'}} 
            onWheel={this.RPL5I} onMouseMove={this.QTAV9} onMouseDown={this.YVW6B} onMouseUp={this.B78WB} >
            <canvas id="textCanvas1" style={{border: "none", display: "none"}} width="64" height="16"></canvas>
            <div style={{margin:'1px', color: "#FFFFFF"}}>
                <div style={{ width: '12px', height: '12px', padding: '0px', display: 'inline-block', border: '1px solid #FFFFFF'}} onClick={() => {this.setState({showInfo: !this.state.showInfo})}}>
                    {this.state.showInfo ? <img style={{width: '12px', height: '12px'}} src={checkIcon} alt="icon" /> : <img style={{width: '12px', height: '12px'}} src={''} alt="" />}
                </div>
                {this.state.unit === "foot" ? 
                <Fragment><input type="radio" id="unitFoot" name="unitGroup" value="foot" onClick={this.onUnitChange} checked /><label for="unit">Foot</label></Fragment> :
                <Fragment><input type="radio" id="unitFoot" name="unitGroup" value="foot" onClick={this.onUnitChange} /><label for="unit">Foot</label></Fragment> 
                }
                {this.state.unit === "meter" ? 
                <Fragment><input type="radio" id="unitMeter" name="unitGroup" value="meter" onClick={this.onUnitChange} checked /><label for="unit">Meter</label></Fragment> :
                <Fragment><input type="radio" id="unitMeter" name="unitGroup" value="meter" onClick={this.onUnitChange} /><label for="unit">Meter</label></Fragment> 
                }
            </div>
        </div>

        <div style={{position:'absolute', bottom:'0px', left: '0px', color:'white'}} 
            onWheel={this.RPL5I} onMouseMove={this.QTAV9} onMouseDown={this.YVW6B} onMouseUp={this.B78WB} >
              <div style={{margin:'4px'}}>
                { this.AU16L !== null && this.AU16L !== undefined && this.state.unit === 'meter' &&
                    <span>Grid size: {this.AU16L.N4AFK} m</span>
                }
                { this.AU16L !== null && this.AU16L !== undefined && this.state.unit !== 'meter' &&
                    <span>Grid size: {this.AU16L.OO0CR} ft</span>
                }
              </div>
        </div>

        { AFV32 &&
        <div style={{position:'absolute', top:'0px', left: '0px', width: "100%", height: "100%",
                    color:'white', backgroundColor: "#000000", display: "flex", 
                    justifyContent: "center", alignItems: "center"}} >
             <div>
             <h2>Loading</h2>
             </div>
        </div>
        }
        { YIUIM &&
        <div style={{position:'absolute', top:'0px', left: '0px', width: "100%", height: "100%",
                    color:'white', backgroundColor: "#000000", display: "flex", 
                    justifyContent: "center", alignItems: "center"}} >
             <div>
             <h2>Fail to load the data</h2>
             </div>
        </div>
        } 
      </div>
    );
  }
}

const SceneComponentEx = () => {
  const { AU16L } = useContext(PetroContext)

  return (
    <SceneComponent AU16L={AU16L} />
  )
}

export default SceneComponentEx;


