export function CXAD0(HRH1K, XSIK3, KQ4R4, X2M96) {
    var I7QRT = mat4.create();
    mat4.identity(I7QRT);
    mat4.rotateZ(I7QRT, I7QRT, X2M96);
    
    var HVK37 = vec3.create();
    vec3.transformMat4(HVK37, [0, KQ4R4, 0], I7QRT);

    mat4.identity(I7QRT);
    mat4.translate(I7QRT, I7QRT, HVK37);
    mat4.rotateZ(I7QRT, I7QRT, X2M96);
    mat4.scale(I7QRT, I7QRT, [HRH1K, XSIK3, 1]);

    return I7QRT;
}

