import * as mat4 from '../glMatrix/mat4';
import { UR287, KLUTF, SDX1A, DYJO1,  GT6Y2, CKUXB, GI4VX, OASCO, SQ829 } from '../sceneConsts';

export function DNBQE(TL63D) {
    this.OT1BU = TL63D;
    this.S58HW = TL63D.S58HW;

    var K1S8Y = OASCO.ERMXX;

    var N0223 = 260;
    var AB15P = 60;

    var DYODA = [0.7, 0.7, 0.7, 1.0];
    var B3B2Q = [1.0, 1.0, 1.0, 1.0];

    var WJ70L = 45;
    var SOGFT = [0.2, 0.2, 0.2, 1];
    var GR3BS = [0.6, 0.6, 0.6, 1];
    
    var CTS4C;
    var ACVJP;
    var BB5HP;
    var FUGGV;

    var T5EDC;
    var NL231;
    
    var VFQ0F;
    var F1WPY;
    
    var E22TF;
    var F3R4O;
    var B1ENY;
    var A77PK;

    var CCMER;
    var NO53O;
    var N8MG4;

    var VVTCJ;
    var WCVQU;
    var PIQFC;
    var SU03C;
    var TR6W0;
    var BSBBQ;
    var PTJ8A;

    this.GBNM5 = function() {
        var EPXCC = 18;
        var EYQFN = 24;

        CTS4C = SDX1A([0, EYQFN, 0], EPXCC, EPXCC, 0);
        ACVJP = SDX1A([EYQFN, 0, 0], EPXCC, EPXCC, -Math.PI / 2.0);
        BB5HP = SDX1A([0, -EYQFN, 0], EPXCC, EPXCC, Math.PI);
        FUGGV = SDX1A([-EYQFN, 0, 0], EPXCC, EPXCC, Math.PI / 2.0);
        
        T5EDC = UR287(64, 64, [0, 0, 0]);
        
        VFQ0F = KLUTF([0, 0, 0], WJ70L - 2);
        F1WPY = KLUTF([0, 0, 0], WJ70L + 2);
        NL231 = KLUTF([0, 0, 0], 18);

        E22TF = KLUTF([0, -80, 0], 16);
        F3R4O = KLUTF([0, -80, 0], 18);
        B1ENY = KLUTF([0, -130, 0], 16);
        A77PK = KLUTF([0, -130, 0], 18);

        CCMER = DYJO1([0, -80, 0], 20, 4);
        NO53O = DYJO1([0, -80, 0], 4, 20);
        N8MG4 = DYJO1([0, -130, 0], 20, 4);

        this.OOV4D = GT6Y2(8);

        SU03C = CKUXB(this.OOV4D);
        TR6W0 = CKUXB(this.OOV4D + 1);
        BSBBQ = CKUXB(this.OOV4D + 2);
        PTJ8A = CKUXB(this.OOV4D + 3);

        WCVQU = CKUXB(this.OOV4D + 4);
        PIQFC = CKUXB(this.OOV4D + 5);
        VVTCJ = CKUXB(this.OOV4D + 6);
    };

    this.KX8KR = function (DLXD5, WV2JJ) {

        if (WV2JJ.RKI2K !== 0) return;
        
        K1S8Y = OASCO.ERMXX;

        if (DLXD5 === this.OOV4D) {
            K1S8Y = OASCO.XAD2I;
            WV2JJ.VUGJ9 = OASCO.XAD2I;
            WV2JJ.RKI2K = 1;
            return;
        }
        if (DLXD5 === (this.OOV4D + 1)) {
            K1S8Y = OASCO.HUDWF;
            WV2JJ.VUGJ9 = OASCO.HUDWF;
            WV2JJ.RKI2K = 1;
            return;
        }
        if (DLXD5 === (this.OOV4D + 2)) {
            K1S8Y = OASCO.NX9V9;
            WV2JJ.VUGJ9 = OASCO.NX9V9;
            WV2JJ.RKI2K = 1;
            return;
        }
        if (DLXD5 === (this.OOV4D + 3)) {
            K1S8Y = OASCO.P5S0N;
            WV2JJ.VUGJ9 = OASCO.P5S0N;
            WV2JJ.RKI2K = 1;
            return;
        }
        if (DLXD5 === (this.OOV4D + 4)) {
            K1S8Y = OASCO.G0TII;
            WV2JJ.VUGJ9 = OASCO.G0TII;
            WV2JJ.RKI2K = 1;
            return;
        }
        if (DLXD5 === (this.OOV4D + 5)) {
            K1S8Y = OASCO.HYW3Y;
            WV2JJ.VUGJ9 = OASCO.HYW3Y;
            WV2JJ.RKI2K = 1;
            return;
        }
        if (DLXD5 === (this.OOV4D + 6)) {
            K1S8Y = OASCO.IAXIK;
            WV2JJ.VUGJ9 = OASCO.IAXIK;
            WV2JJ.RKI2K = 1;
            return;
        }
        WV2JJ.RKI2K = 0;
    };

    this.KCBLT = function() {
        var AWAME = mat4.create();
        mat4.identity(AWAME);

        var XGW5I = mat4.create();
        mat4.identity(XGW5I);

        var QGHIE = mat4.create();
        mat4.identity(QGHIE);

        mat4.identity(AWAME);
        mat4.ortho(AWAME, -this.S58HW.viewportWidth / 2, this.S58HW.viewportWidth / 2, -this.S58HW.viewportHeight / 2, this.S58HW.viewportHeight / 2, -500, 500);

        var CMKXP = -this.S58HW.viewportWidth / 2 + AB15P;
        var Q6Q9A = -this.S58HW.viewportHeight / 2 + N0223;

        mat4.identity(XGW5I);
        mat4.lookAt(XGW5I, [CMKXP, Q6Q9A, 100], [CMKXP, Q6Q9A, -100], [0, 1, 0]);

        mat4.identity(QGHIE);
        mat4.multiply(QGHIE, AWAME, XGW5I);
        this.S58HW.uniformMatrix4fv(this.OT1BU.QI65W, false, QGHIE);

        this.S58HW.uniform1i(this.OT1BU.DQSLD, SQ829.P5KEV);
        this.S58HW.uniform1i(this.OT1BU.PQDJ6, SQ829.P5KEV);
        this.S58HW.depthFunc(this.S58HW.ALWAYS);

        this.S58HW.enable(this.S58HW.BLEND);
        this.S58HW.disable(this.S58HW.DEPTH_TEST);
        this.S58HW.blendFunc(this.S58HW.SRC_ALPHA, this.S58HW.ONE_MINUS_SRC_ALPHA);

        if (this.OT1BU.YUWA5) {
                this.OT1BU.RGPSB.KCBLT(F3R4O, WCVQU);
                this.OT1BU.RGPSB.KCBLT(A77PK, PIQFC);

                this.OT1BU.XPWNO.KCBLT(CTS4C, SU03C);
                this.OT1BU.XPWNO.KCBLT(ACVJP, TR6W0);
                this.OT1BU.XPWNO.KCBLT(BB5HP, BSBBQ);
                this.OT1BU.XPWNO.KCBLT(FUGGV, PTJ8A);

                this.OT1BU.Y6DKG.KCBLT(NL231, VVTCJ);
        }
        else {
                this.OT1BU.RGPSB.KCBLT(F1WPY, GR3BS);
                this.OT1BU.RGPSB.KCBLT(VFQ0F, SOGFT);

                this.OT1BU.RGPSB.KCBLT(F3R4O, GR3BS);
                this.OT1BU.RGPSB.KCBLT(E22TF, SOGFT);

                this.OT1BU.RGPSB.KCBLT(A77PK, GR3BS);
                this.OT1BU.RGPSB.KCBLT(B1ENY, SOGFT);

                this.OT1BU.XPWNO.KCBLT(CTS4C, K1S8Y === OASCO.XAD2I ? B3B2Q : DYODA);
                this.OT1BU.XPWNO.KCBLT(ACVJP, K1S8Y === OASCO.HUDWF ? B3B2Q : DYODA);
                this.OT1BU.XPWNO.KCBLT(BB5HP, K1S8Y === OASCO.NX9V9 ? B3B2Q : DYODA);
                this.OT1BU.XPWNO.KCBLT(FUGGV, K1S8Y === OASCO.P5S0N ? B3B2Q : DYODA);

                this.OT1BU.Y6DKG.KCBLT(CCMER, K1S8Y === OASCO.G0TII ? B3B2Q : DYODA);
                this.OT1BU.Y6DKG.KCBLT(NO53O, K1S8Y === OASCO.G0TII ? B3B2Q : DYODA);
                this.OT1BU.Y6DKG.KCBLT(N8MG4, K1S8Y === OASCO.HYW3Y ? B3B2Q : DYODA);


            if (K1S8Y === OASCO.IAXIK) {
                this.OT1BU.RGPSB.KCBLT(NL231, B3B2Q);
            }

            this.OT1BU.Q81IG.EIU18();

            if (!this.OT1BU.TH_pickingEnabled) {
                this.OT1BU.Q81IG.A9YUG(T5EDC, this.OT1BU.C5GXA[GI4VX.M3LCM]);
            }

            this.OT1BU.Q81IG.K6TCJ();            
        }


        this.S58HW.disable(this.S58HW.BLEND);
        this.S58HW.enable(this.S58HW.DEPTH_TEST);

        this.S58HW.depthFunc(this.S58HW.LESS);
        this.S58HW.disableVertexAttribArray(this.OT1BU.J452X);
        this.S58HW.uniform1i(this.OT1BU.DQSLD, SQ829.B0SJ4);
        this.S58HW.uniform1i(this.OT1BU.PQDJ6, SQ829.B0SJ4);
    }
   
}
