import * as mat4 from '../glMatrix/mat4';
import * as vec3 from '../glMatrix/vec3';

export function OYKD8(TL63D) {
    this.N2D5R = [];
    this.NPFJJ = [];
    
    this.KH2FX = [];
    this.BXR0Q = [];
    this.TG2NI = 1;
    this.QQ76F = 1;

    this.XJVI0 = [];
    this.QC26K = [];
    this.O07Q7 = [];
    this.KLARI = [];
    
    this.S58HW = TL63D.S58HW;
    this.OT1BU = TL63D;

    this.GBNM5 = function(DMQD7, G2B3S, I1V4M) {
        this.EUY4N = DMQD7;
        this.YT6B9 = G2B3S;
        this.O3B3W = I1V4M;

        this.N2D5R = [this.EUY4N[0], this.EUY4N[1], this.EUY4N[2]];
        this.NPFJJ = [this.YT6B9[0], this.YT6B9[1], this.YT6B9[2]];

        var YJ5G1 = vec3.create();
        vec3.subtract(YJ5G1, this.YT6B9, this.EUY4N);

        this.TS07H();
        this.UMMIS = this.DTVJS;
        this.HAPUI = DMQD7;
    };

    this.FMGVC = function(mouseX, mouseY)
    {
        var BDAG2 = this.S58HW.viewportWidth / 2.0;
        var E6AGF = this.S58HW.viewportHeight / 2.0;

        var MQPLL = BDAG2 / E6AGF;
        var ES7J0 = Math.tan(Math.PI / 8) * 1000.0;
        var H5YM5 = ES7J0 * MQPLL;

        var CMKXP = (mouseX - BDAG2) / BDAG2 * H5YM5;
        var Q6Q9A = (E6AGF - mouseY) / E6AGF * ES7J0;

        this.UMMIS = vec3.create();
        vec3.scale(this.UMMIS, this.DTVJS, 1000);

        var XDVSW = vec3.create();
        vec3.scale(XDVSW, this.VIHAY, CMKXP);

        var F421O = vec3.create();
        vec3.scale(F421O, this.K04I4, Q6Q9A);

        vec3.add(this.UMMIS, this.UMMIS, XDVSW);
        vec3.add(this.UMMIS, this.UMMIS, F421O);
        vec3.normalize(this.UMMIS, this.UMMIS);

        this.HAPUI = [this.EUY4N[0], this.EUY4N[1], this.EUY4N[2]];
    }

    this.BVCHA = function () {
        var SCC2G = mat4.create();
        mat4.identity(SCC2G);
        mat4.rotate(SCC2G, SCC2G, Math.PI / 4, this.VIHAY);
        mat4.rotate(SCC2G, SCC2G, -Math.PI * 10 / 180, [0, 1, 0]);

        var OSCQO = this.DEWR1() + Math.PI * 3 / 4;
        var N9BFG = [Math.cos(OSCQO), 1, Math.sin(OSCQO)];
        vec3.normalize(N9BFG, N9BFG);

        this.S58HW.uniform3fv(this.OT1BU.GXVHC, N9BFG);

        var AWAME = mat4.create();
        mat4.identity(AWAME);

        mat4.perspective(AWAME, Math.PI / 4.0, this.S58HW.viewportWidth / this.S58HW.viewportHeight, 1, this.O3B3W * 2.0);

        var XGW5I = mat4.create();
        mat4.identity(XGW5I);

        mat4.lookAt(XGW5I, this.EUY4N, this.YT6B9, this.K04I4);
        var QGHIE = mat4.create();
        mat4.identity(QGHIE);
        mat4.multiply(QGHIE, AWAME, XGW5I);

        this.S58HW.uniformMatrix4fv(this.OT1BU.QI65W, false, QGHIE);
    };

    this.TS07H = function() {
        this.DTVJS = vec3.create();
        vec3.subtract(this.DTVJS, this.YT6B9, this.EUY4N);
        vec3.normalize(this.DTVJS, this.DTVJS);

        if (this.DTVJS[0] === 0 && this.DTVJS[2] === 0 && (this.DTVJS[1] === 1 || this.DTVJS[1] === -1)) {
            this.K04I4 = [0, 0, -1];
            this.VIHAY = [1, 0, 0];
        }
        else {
            this.VIHAY = vec3.create();
            vec3.cross(this.VIHAY, this.DTVJS, [0, 1, 0]);
            vec3.normalize(this.VIHAY, this.VIHAY);

            this.K04I4 = vec3.create();
            vec3.cross(this.K04I4, this.VIHAY, this.DTVJS);
            vec3.normalize(this.K04I4, this.K04I4);
        }
    };

    this.BPQI1 = function() {
        this.NR27F(this.N2D5R, this.NPFJJ);
    }

    this.NR27F = function(Y2VKX, TRFKX) {
        this.TG2NI = vec3.distance(this.YT6B9, this.EUY4N);
        this.QQ76F = vec3.distance(Y2VKX, TRFKX);

        this.XJVI0 = [this.DTVJS[0], this.DTVJS[1], this.DTVJS[2]];
        this.QC26K = [this.K04I4[0], this.K04I4[1], this.K04I4[2]];

        this.O07Q7 = vec3.create();
        vec3.subtract(this.O07Q7, TRFKX, Y2VKX);
        vec3.normalize(this.O07Q7, this.O07Q7);

        this.KLARI = vec3.create();
        if (this.O07Q7[0] === 0 && this.O07Q7[2] === 0 && (this.O07Q7[1] === 1 || this.O07Q7[1] === -1)) {
            vec3.cross(this.KLARI, this.VIHAY, this.O07Q7);
        }
        else {
            vec3.cross(this.KLARI, this.O07Q7, [0, 1, 0]);
            vec3.normalize(this.KLARI, this.KLARI);
            vec3.cross(this.KLARI, this.KLARI, this.O07Q7);
        }
        vec3.normalize(this.KLARI, this.KLARI);

        this.KH2FX = [this.YT6B9[0], this.YT6B9[1], this.YT6B9[2]];
        this.BXR0Q = [TRFKX[0], TRFKX[1], TRFKX[2]];
    };
   
    this.O9T2J = function(UD3JH) {
        var QEH5H = vec3.create();
        vec3.subtract(QEH5H, this.YT6B9, this.EUY4N);
        var VBO4I = vec3.length(QEH5H);

        if (UD3JH === 0) {
            this.NR27F([this.YT6B9[0], this.YT6B9[1], this.YT6B9[2] + VBO4I],
                            [this.YT6B9[0], this.YT6B9[1], this.YT6B9[2]]);
        }
        else if (UD3JH === 1) {
            this.NR27F([this.YT6B9[0] - VBO4I, this.YT6B9[1], this.YT6B9[2]],
                            [this.YT6B9[0], this.YT6B9[1], this.YT6B9[2]]);
        }
        else if (UD3JH === 2) {
            this.NR27F([this.YT6B9[0], this.YT6B9[1], this.YT6B9[2] - VBO4I],
                            [this.YT6B9[0], this.YT6B9[1], this.YT6B9[2]]);
        }
        else if (UD3JH === 3) {
            this.NR27F([this.YT6B9[0] + VBO4I, this.YT6B9[1], this.YT6B9[2]],
                            [this.YT6B9[0], this.YT6B9[1], this.YT6B9[2]]);
        }
        else if (UD3JH === 4) {
            this.NR27F([this.YT6B9[0], this.YT6B9[1] + VBO4I, this.YT6B9[2]],
                            [this.YT6B9[0], this.YT6B9[1], this.YT6B9[2]]);
        }
    };

    this.DOAXT = function(MQPLL) {
        this.DTVJS = [this.XJVI0[0] + (this.O07Q7[0] - this.XJVI0[0]) * MQPLL,
                       this.XJVI0[1] + (this.O07Q7[1] - this.XJVI0[1]) * MQPLL,
                       this.XJVI0[2] + (this.O07Q7[2] - this.XJVI0[2]) * MQPLL];

        this.K04I4 = [this.QC26K[0] + (this.KLARI[0] - this.QC26K[0]) * MQPLL,
                       this.QC26K[1] + (this.KLARI[1] - this.QC26K[1]) * MQPLL,
                       this.QC26K[2] + (this.KLARI[2] - this.QC26K[2]) * MQPLL];

        vec3.normalize(this.DTVJS, this.DTVJS);
        vec3.normalize(this.K04I4, this.K04I4);
        vec3.cross(this.VIHAY, this.DTVJS, this.K04I4);
        vec3.normalize(this.VIHAY, this.VIHAY);

        this.YT6B9 = [this.KH2FX[0] + (this.BXR0Q[0] - this.KH2FX[0]) * MQPLL,
                           this.KH2FX[1] + (this.BXR0Q[1] - this.KH2FX[1]) * MQPLL,
                           this.KH2FX[2] + (this.BXR0Q[2] - this.KH2FX[2]) * MQPLL];

        var CG6LJ = this.TG2NI + (this.QQ76F - this.TG2NI) * MQPLL;

        var FJ0V5 = vec3.create();
        vec3.scale(FJ0V5, this.DTVJS, -CG6LJ);
        vec3.add(this.EUY4N, this.YT6B9, FJ0V5);
    };

    this.KMA6D = function(BQ4CT) {
        BQ4CT = BQ4CT > 0 ? 1 : -1;
        var YJ5G1;
        YJ5G1 = vec3.create();
        vec3.subtract(YJ5G1, this.YT6B9, this.EUY4N);

        var MYYEM = this.O3B3W / 500.0;
        var MENWP = this.O3B3W / 10.0;
        var H4SLX = vec3.length(YJ5G1) / this.O3B3W;

        MYYEM = MYYEM + (MENWP - MYYEM) * H4SLX;
        var JQMXK = vec3.distance(this.YT6B9, this.EUY4N);
        var X3P1B = JQMXK - MYYEM * BQ4CT;

        if (X3P1B > this.O3B3W) return;

        if (BQ4CT > 0 && MYYEM > vec3.distance(this.YT6B9, this.EUY4N)) return;

        vec3.normalize(YJ5G1, YJ5G1);
        vec3.scale(YJ5G1, YJ5G1, BQ4CT * MYYEM / 2);
        vec3.add(this.EUY4N, this.EUY4N, YJ5G1);
    };

    this.VNSDS = function (HA80A, WCOYT) {
        HA80A = -HA80A * Math.PI / 180;
        WCOYT = -WCOYT * Math.PI / 180;
        
        var Q64E1 = mat4.create();
        mat4.identity(Q64E1);

        var GG4TQ = [0, 1, 0];
        var QFCAT = mat4.create();
        mat4.rotate(QFCAT, Q64E1, HA80A, GG4TQ);

        mat4.identity(Q64E1);
        var RNRUA = mat4.create();
        mat4.rotate(RNRUA, Q64E1, WCOYT, this.VIHAY);

        var QNDN0 = mat4.create();
        mat4.multiply(QNDN0, QFCAT, RNRUA);

        var YJ5G1 = vec3.create();
        vec3.subtract(YJ5G1, this.EUY4N, this.YT6B9);
        vec3.transformMat4(YJ5G1, YJ5G1, QNDN0); 

        vec3.add(this.EUY4N, this.YT6B9, YJ5G1);

        vec3.transformMat4(this.DTVJS, this.DTVJS, QNDN0);
        vec3.normalize(this.DTVJS, this.DTVJS);

        vec3.transformMat4(this.K04I4, this.K04I4, QNDN0);
        vec3.normalize(this.K04I4, this.K04I4);

        vec3.cross(this.VIHAY, this.DTVJS, this.K04I4);
        vec3.normalize(this.VIHAY, this.VIHAY);
    };

    this.DEWR1 = function() {
        return Math.atan2(this.DTVJS[0], -this.DTVJS[2]);
    };

    this.QCOBR = function() {
        var N8BK6 = 0;    
        if (this.DTVJS.X === 0.0 && this.DTVJS.Z === 0.0)
        {
            if (this.DTVJS.Y < 0.0)
            {
                N8BK6 = Math.PI;
            }
        }
        else
        {
            N8BK6 = Math.PI / 2 - Math.atan2(-this.DTVJS[1], Math.sqrt(this.DTVJS[0] * this.DTVJS[0] + this.DTVJS[2] * this.DTVJS[2]));
        }

        return N8BK6;
    };


}