import React, { Fragment, useContext, useState, useEffect } from "react";
import { makeStyles } from '@material-ui/core/styles';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { MPTL2 } from '../services/userService';
import { withRouter } from "react-router-dom";
import { PetroContext } from '../context/petroContext';
import { Button } from '@material-ui/core';
import HeaderComponent from '../header';
import FooterComponent from '../footer';
import ConfirmDialog from '../components/dialogs/confirmDialog';

const useStyles = makeStyles({
    root: {
      boxShadow: '0 0 6px 6px rgba(200, 200, 200, .3)',
      width:"400px",
      minHeight:'440px',
      padding: "20px"
    },
    title: {
        fontSize: '30px',
        fontWeight:'bold',
        marginTop: '20px',
        marginBottom:"30px",
    },
    label: {
        marginTop: '20px',
        fontSize: '20px',
    },
    input: {
        fontSize: '16px',
        width: '100%',
        padding: '6px',
        marginTop: '10px',
        marginBottom:'10px'
    },
    sumbit: {
        marginTop: '20px',
        textAlign:'center',
    },
    error: {
        color: 'red'
    },
    responseError: {
        textAlign: 'left',
        padding:'10px',
        backgroundColor: '#F8D7DA',
        color: 'black'
    },
    viewerRow: {
        borderBottom: '1px solid gray',         
        display:'flex',
        flexDirection:'row', 
        justifyContent:"space-between",
        alignItems:"center",
        padding:'4px',
        '&:hover': {
            // cursor: "pointer",
            // backgroundColor: 'lightgray'
          }
    },

  });


const MyViewersComponent = (props) => {
    const { Token } = useContext(PetroContext)
    const classes = useStyles();
    const [viewers, setViewers] = useState(null)
    const [deleteInfo, setDeleteInfo] = useState(null);

    const RLP4L = () => {
        MPTL2.RLP4L(Token).then(resp => {
            setViewers(resp.data);
        })
    }

    const IIR9P = () => {
        if (deleteInfo !== null) {
            MPTL2.IIR9P(deleteInfo.userId, Token).then(resp => {
                RLP4L();
                setDeleteInfo(null);
            }, err => {
                alert("Fail to remove viewer " + deleteInfo.username)
            })
        }
    }

    useEffect(() => {
        let userInfo = MPTL2.OF5G9();
        if (userInfo !== null && userInfo.role === "User") {
            props.history.push('/');
        }
        RLP4L();
    }, []);

    const getRow = (element) => {
        return (
          <div className={classes.viewerRow}>
              <div>{element.username}</div>
              <div>                                
                  <Button variant="contained" color="primary" 
                    style={{textTransform:'capitalize', backgroundColor:'#0B74A4', color:'#FFFFFF'}}
                    onClick={e => {
                        setDeleteInfo({open: true, userId:element.userId, username: element.username});
                      }}>
                     Remove
                   </Button>
              </div>
          </div>
        )
      }

    return (
      <Fragment>
        <HeaderComponent />
        <div style={{paddingTop: '170px', paddingLeft:"10%", paddingRight:"10%", paddingBottom:'20px' }}>
            <div style={{display:'flex', flexDirection:'row', justifyContent:'center'}}>
            <div className={classes.root} >
            <div className={classes.title} >Add Viewer</div>
                <Formik
                    initialValues={{
                        email: '',
                        // password: '',
                        // confirmPassword: '',
                    }}
                    validationSchema={Yup.object().shape({
                        email: Yup.string().required("Email is reuqired").email('Invalid email'),
                        // password: Yup.string().required('Password is required').min(6, 'Minimum 6 characters').max(30, 'Maximum 30 characters')
                        //         .oneOf([Yup.ref('confirmPassword'), null], 'Passwords must match'),
                        // confirmPassword: Yup.string().required('Confirm password is required').min(6, 'Minimum 6 characters').max(30, 'Maximum 30 characters')
                        //         .oneOf([Yup.ref('password'), null], 'Passwords must match')
                    })}
                    onSubmit={({ email }, { setStatus, setSubmitting }) => {
                        setStatus();
                        MPTL2.UI0J1(email, email, Token).then(res => {
                            RLP4L();
                        }, err => { alert('Fail to add viewer')})
                    }}
                    render={({ errors, status, touched, isSubmitting }) => (
                        <Form>                          
                            <div>
                                <span className={classes.label}>Email</span>
                            </div>
                            <div>
                                <Field name="email" type="text" className={classes.input} />
                            </div>
                            <div>
                                <ErrorMessage name="email" component="div" className={classes.error} />
                            </div>

                            {/* <div style={{marginTop:'20px'}}>
                                <span className={classes.label}>Password</span>
                            </div>
                            <div>
                                <Field name="password" type="password" className={classes.input} />
                            </div>
                            <div>
                                <ErrorMessage name="password" component="div" className={classes.error} />
                            </div> */}

                            {/* <div style={{marginTop:'20px'}}>
                                <span className={classes.label}>Confirm Password</span>
                            </div>
                            <div>
                                <Field name="confirmPassword" type="password" className={classes.input} />
                            </div>
                            <div>
                                <ErrorMessage name="confirmPassword" component="div" className={classes.error} />
                            </div> */}
                            <div className={classes.sumbit}>
                                <Button type="submit" variant="contained" color="primary" style={{width:'100%', textTransform:'capitalize', backgroundColor:'#0B74A4', color:'#FFFFFF'}}>
                                    Add Viewer
                                </Button>
                            </div>
                        </Form>
                    )}
                />                

            </div>

            <div className={classes.root} style={{marginLeft:'50px'}}>
            <div className={classes.title} >My Viewers</div>
            <div style={{height:'350px', overflowY:'auto'}}>
               {viewers &&
                viewers.map( viewer => {
                return getRow(viewer);
                })
               }
            </div>
            </div>

            </div>      
        </div>
        <FooterComponent />
        { deleteInfo &&
        <ConfirmDialog open={deleteInfo.open} title="Delete confirmation" content={"Are you sure to delete " + deleteInfo.username + " viewer?"} 
          handleConfirmAction={IIR9P} handleConfirmCancel={() => {setDeleteInfo(null)}} />
        }

      </Fragment>   
    );

}

export default withRouter(MyViewersComponent);

