import React, { useState, useEffect, useContext } from 'react';
import { Button, Dialog, DialogTitle, DialogContent, Checkbox } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { KLXR3 } from '../../services/projectService';
import { PetroContext } from '../../context/petroContext';

const useStyles = makeStyles({
    root: {
      boxShadow: '0 0 6px 6px rgba(200, 200, 200, .3)',
      width:"400px",
      minHeight:'440px',
      padding: "20px"
    },
    title: {
        fontSize: '30px',
        fontWeight:'bold',
        marginTop: '20px',
        marginBottom:"10px",
    },
    subTitle: {
        fontSize: '20px',
        fontWeight:'normal',
        marginTop: '0px',
        marginBottom:"0px",
    },
    label: {
        marginTop: '20px',
        fontSize: '20px',
    },
    input: {
        fontSize: '16px',
        width: '100%',
        padding: '6px',
        marginTop: '10px',
        marginBottom:'10px'
    },
    sumbit: {
        marginTop: '20px',
        textAlign:'center',
    },
    error: {
        color: 'red'
    },
    responseError: {
        textAlign: 'left',
        padding:'10px',
        backgroundColor: '#F8D7DA',
        color: 'black'
    },
    viewerRow: {
        borderBottom: '1px solid lightgray',         
        display:'flex',
        flexDirection:'row', 
        justifyContent:"space-between",
        alignItems:"center",
        padding:'4px',
        '&:hover': {
            // cursor: "pointer",
            backgroundColor: 'lightgray'
          }
    },

  });

const ShareProjectDialog = (props) => {
    const classes = useStyles();
    const [viewers, setViewers] = useState(null)
    const { Token } = useContext(PetroContext);

    const loadSharedViewers = () => {
        if (Token) {
            KLXR3.OEN90(props.projectId, Token).then(resp => {
                setViewers(resp.data);
            })
        }
    }

    useEffect(() => {
        loadSharedViewers();
    }, [props.open]);
    
    const updateViewer = (id, checked) => {
        let viewerList = viewers.slice(0);
        let viewer = viewerList.find(u => u.userId === id);
        if (viewer) {
            viewer.shared = checked;
            setViewers(viewerList);
        }
    }

    const getRow = (element) => {
        return (
          <div className={classes.viewerRow}>
              <div style={{marginLeft:'20px'}}>{element.username}</div>
              <div style={{marginRight:'40px'}}>                                
                <Checkbox onChange={(e) => { updateViewer(element.userId, e.target.checked)} }
                    checked={element.shared}
                    value="secondary"
                    color="primary"
                    inputProps={{ 'aria-label': 'scondary checkbox' }}
                />
              </div>
          </div>
        )
      }    

    const LAA72 = () => {
        if (Token) {
            let data = [];
            viewers.forEach(element => {
                data.push({userId: element.userId, shared: element.shared})
            }); 
            KLXR3.LAA72({projectId: props.projectId, SharedUsers: data}, Token).then(res => {
                alert('Updated')
            }, err => { 
                alert('Failed')
            })    
        }
    }
    
    return (
        <Dialog open={props.open} aris-labelledby="form-dialog-title" maxWidth={'sm'} fullWidth={true}>
            <DialogTitle id="form-dialog-title">
                <div className={classes.title}>Share project</div>
                {props.projectName && <div className={classes.subTitle}>({props.projectId}) {props.projectName}</div>}
            </DialogTitle>
            <DialogContent>
                <div style={{height:'300px', borderWidth: '1px', borderColor: 'lightgray', borderStyle:'solid',
                           padding: '0px', overflow:"hidden", overflowY:'auto'}}>
                    {viewers &&
                        viewers.map( viewer => {
                        return getRow(viewer);
                        })
                    }
                </div>
                <div style={{display:'flex', justifyContent: 'flex-end', marginTop:'20px', marginBottom:'20px'}}>
                    <Button style={{marginRight: '30px', textTransform:'none'}}  size="medium" variant="contained" color="primary" onClick={LAA72} >Update</Button>
                    <Button style={{marginRight: '10px', textTransform:'none'}}  size="medium" variant="contained" onClick={() => props.handleDialogClose()} >Close</Button>
                </div>
            </DialogContent>
        </Dialog>
    )
}

export default ShareProjectDialog;
