import React, { useContext } from "react";
import { BrowserRouter as Router, Route, Redirect } from "react-router-dom";
import RootComponent from "./root";
import HomeComponent from "./home";
import AppComponent from "./app";
import DashboardComponent from "./dashboard";
import LoginComponent from "./login/login";
import SignUpComponent from "./login/signup";
import ForgetComponent from "./login/forgetPassword";
import ChangePasswordComponent from './login/changePassword';
import ResetPasswordComponent from './login/resetpassword';
import MyViewersComponent from './login/myViewers';
import MyAccountComponent from './login/myAccount';
import ProtectedRoute from './protectedRoute';
import { PetroContext } from './context/petroContext';

const AppStartComponent = (props) => {
    const { CurrentUser, dispatchAction } = useContext(PetroContext);
    if (CurrentUser === null) {
        let loginTime = localStorage.getItem('loginTime');
        if (loginTime) {
            let currentTime = new Date().getTime();
            if ((currentTime - loginTime) < 6 * 3600000) {
                let loginData = localStorage.getItem('loginData');
                if (loginData) {
                    let userData = JSON.parse(loginData);
                    dispatchAction({type:'USER_LOGIN', data:userData});
                    dispatchAction({type:'SET_TOKEN_DATA', data:userData.token});
                }
            }
        }
    }

    return (
      <Router>
        <Route exact path="/" component={RootComponent} />
        <Route path="/home" component={HomeComponent} />
        <Route path="/signin" component={LoginComponent} />
        <Route path="/signup" component={SignUpComponent} />
        <Route path="/myaccount" component={MyAccountComponent} />
        <Route path="/myviewers" component={MyViewersComponent} />
        <Route path="/forgetpassword" component={ForgetComponent} />
        <Route path="/resetpassword" component={ResetPasswordComponent} />
        <Route path="/changepassword" component={ChangePasswordComponent} />
        <ProtectedRoute path="/projects" component={DashboardComponent} />
        <Route path="/project/:id" component={AppComponent} />
      </Router>
    );

}

export default AppStartComponent;

