import React from 'react';
import { Button, Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions } from '@material-ui/core';

const ConfirmDialog = (props) => {
    return (
        <Dialog open={props.open} aris-labelledby="form-dialog-title" maxWidth={'sm'} fullWidth={true}>
            <DialogTitle id="form-dialog-title">{props.title}</DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description">
                    {props.content}
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <div style={{display:'flex', justifyContent: 'flex-end', marginBottom:'10px'}}>
                    <Button style={{marginRight: '40px', textTransform:'none'}} size="medium" variant="contained" color="primary" onClick={() => props.handleConfirmAction()} >Delete</Button>
                    <Button style={{marginRight: '10px', textTransform:'none'}} size="medium" variant="contained" onClick={() => props.handleConfirmCancel()} >Cancel</Button>
                </div>
            </DialogActions>
        </Dialog>
    )
}

export default ConfirmDialog;
