import React, { Fragment, useState, useEffect } from "react";
import { makeStyles } from '@material-ui/core/styles';
import { MPTL2 } from '../services/userService';
import { withRouter } from "react-router-dom";
import HeaderComponent from '../header';
import FooterComponent from '../footer';
import "../styles.css";

const useStyles = makeStyles({
    root: {
      boxShadow: '0 0 6px 6px rgba(200, 200, 200, .3)',
      width:"500px", 
      minHeight: "400px",
      margin:"auto", 
      padding: "20px"
    },
    title: {
        fontSize: '30px',
        fontWeight:'bold',
        marginTop: '20px',
        marginBottom:"10px",
    },
    subTitle: {
        fontSize: '20px',
        fontWeight:'normal',
        marginTop: '20px',
        marginBottom:"10px",
    },

  });


const MyAccountComponent = (props) => {
    const classes = useStyles();
    const [userInfo, setUserInfo] = useState(null)
 
    useEffect(() => {
        setUserInfo(MPTL2.OF5G9());
    }, [])

    return (
      <Fragment>
        <HeaderComponent />
        <div style={{paddingTop: '170px', paddingLeft:"10%", paddingRight:"10%", paddingBottom:'20px' }}>
            <div className={classes.root} >
            <div className={classes.title} >My Account</div>
            {userInfo &&
            <Fragment>
            <div className={classes.subTitle} >{userInfo.name}</div>
            <div className={classes.subTitle} >Expirated on {userInfo.expiration}</div>
            <div className={classes.subTitle} >{userInfo.days} days left</div>
            </Fragment>
            }
            </div>
        </div>
        <FooterComponent />
      </Fragment>   
    );

}

export default withRouter(MyAccountComponent);

