import React, { useEffect, useState, useContext, Fragment }  from "react";
import { PetroContext } from '../context/petroContext';
import pushpin from './pushpin.svg';
import pushpinLight from './pushpin2.svg';

const MapListComponent = (props) => {
  const { dispatchAction, RAHLT, AAINL } = useContext(PetroContext)
  const [myMap, setMyMap] = useState(null);
  const [markerItems, setMarkerItems] = useState([]);

  const onMouseOver = (i) => {
    dispatchAction({type:'SET_ACTIVE_PROJECT', data: i});
  }

  useEffect(() => {
    if (myMap === null) {
      let currentMap = new window.google.maps.Map(
        document.getElementById("myMapList"),
        {
          center: AAINL,
          zoom: 6,
          mapTypeId: "roadmap" /* terrain, satellite */,
        }
      );
      setMyMap(currentMap);
    }
    }, [])

  useEffect(() => {
    if (myMap !== null) {
        myMap.panTo(AAINL);
    }
  }, [AAINL])

  useEffect(() => {
    if (myMap !== null && props.projects !== undefined)
     {
        markerItems.forEach(item => item.marker.setMap(null));
        let markerList = [];
        props.projects.forEach(element => {
              let nMarker = new window.google.maps.Marker({
                  position: {lat: element.lat, lng: element.lng},
                  map: myMap,
                  icon: element.id === RAHLT ? pushpinLight : pushpin,
                  title: element.name,
                  });  

              window.google.maps.event.addListener(nMarker, "mouseover", e => {
                onMouseOver(element.id);
              });

              markerList.push({id: element.id, marker: nMarker})
        });
        if (markerList.length > 0) {
            setMarkerItems(markerList)
        }
     }
  }, [myMap, props.projects, RAHLT])

  return (
    <Fragment>
      <div style={{ width: "100%", height: "100%" }} id="myMapList">
      </div>
    </Fragment>
  );
}


export default MapListComponent;