  export function LAGM9(APTCT) {  
    const O40TQ = BQMVT(APTCT, APTCT.VERTEX_SHADER, CVIIJ(RX43I, 1));
    const TN5KB = BQMVT(APTCT, APTCT.FRAGMENT_SHADER, CVIIJ(NWS5P, 1));
  
    const NNWLY = APTCT.createProgram();
    APTCT.attachShader(NNWLY, O40TQ);
    APTCT.attachShader(NNWLY, TN5KB);
    APTCT.linkProgram(NNWLY);
  
    if (!APTCT.getProgramParameter(NNWLY, APTCT.LINK_STATUS)) {
      return null;
    }

    APTCT.useProgram(NNWLY); 

    return NNWLY;
  }
  
  function BQMVT(APTCT, atype, source) {
    const DKIUS = APTCT.createShader(atype);
  
    APTCT.shaderSource(DKIUS, source);
    APTCT.compileShader(DKIUS);
  
    if (!APTCT.getShaderParameter(DKIUS, APTCT.COMPILE_STATUS)) {
        APTCT.deleteShader(DKIUS);
        return null;
    }
  
    return DKIUS;
  }