import React, { useEffect, Fragment }  from "react";
import { makeStyles } from '@material-ui/core/styles';
import { Button } from '@material-ui/core';
import HeaderComponent from './header';
import FooterComponent from './footer';
import "./styles.css";
import image01 from './images/home01.png'
import image02 from './images/home02.png'
import image03 from './images/home03.png'
import image04 from './images/home04.png'

const useStyles = makeStyles({
  root: {
    boxShadow: '0 0 6px 6px rgba(200, 200, 200, .3)',
    borderRadius:'10px',
    width: "80%",
    minHeight: '440px',
    margin: 'auto'
  },
  box1: {
    backgroundColor: '#EEEEEE',
    paddingBottom: '40px',
    textAlign:'center',
  },
  box1Container: {
    width: '100%',
    margin:'auto',
    textAlign: 'center',
  },
  box1Content: {
    padding: '10px',
    textAlign: 'center',
    marginBottom: '30px',
    fontSize: '30px', 
    fontWeight: 'bold',
    color: '#FFFFFF',
    backgroundColor: '#4EAF54',
    paddingLeft: '40px',
    paddingRight: '40px',
  },
  box2: {
    backgroundColor: '#EEEEEE',
    paddingBottom: '40px',
    textAlign:'center',
  },
});

const HomeComponent = (props) => {
  const classes = useStyles();

  const openDemoProject = (id) => {
      let newWindow = window.open(window.location.origin + `/?id=99999999`, "_blank", "fullscreen=1, location=0, toolbar=0, menubar=0, resizable=1");
      newWindow.moveTo(0,0);
      newWindow.resizeTo(window.screen.width, window.screen.height)
  }

  return (
    <Fragment>
      <HeaderComponent />
      <div style={{ paddingTop: '150px'}}>

          <div style={{ width: "80%", margin: 'auto', borderRadius: "20px", padding: '30px', textAlign: 'center' }}>
            <div style={{marginBottom:'20px'}}>
              <span style={{marginLeft:'10px', marginRight:"2px", color: '#0B74A4', fontFamily: 'Arial', fontSize: '30px', fontWeight:'bolder'}}>An online 3D visualization application for geophysicists and geologists</span>
            </div>
            <Button style={{backgroundColor: "#C85050", color:"white", paddingLeft:'20px', paddingRight:'20px', textTransform:'none'}}
             onClick={() => { openDemoProject() }} >
               <span style={{ fontSize: "20px", color: "#FFFFFF", fontWeight: "bold" }}>View demo project</span>
            </Button>
          </div>

          <div className={classes.box1}>
            <div className={classes.box1Container}>
              <div className={classes.box1Content} >
                  <span>High performance 3D rendering with easy navigation</span>
              </div>
              <div>
                 <img src={image03} alt="" />
              </div>
            </div>
          </div>
          
          <div className={classes.box2}>
            <div className={classes.box1Container}>
              <div className={classes.box1Content} >
                  <span>Create project in the cloud and manage online</span>
              </div>
              <div>
                 <img src={image01} alt="" />
              </div>
            </div>
          </div>

          <div className={classes.box1}>
            <div className={classes.box1Container}>
              <div className={classes.box1Content} >
                  <span>Build project online with context menu</span>
              </div>
              <div>
                 <img src={image02} alt="" />
              </div>
            </div>
          </div>

          <div className={classes.box2}>
            <div className={classes.box1Container}>
              <div className={classes.box1Content} >
                  <span>MicroSeismic events animation with fracture data</span>
              </div>
              <div>
                 <img src={image04} alt="" />
              </div>
            </div>
          </div>

      </div>
      <FooterComponent />
    </Fragment>
  );

}

export default HomeComponent;

