import React, { Fragment, useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { MPTL2 } from '../services/userService';
import { withRouter } from "react-router-dom";
import { Button } from '@material-ui/core';
import HeaderComponent from '../header';
import FooterComponent from '../footer';
import "../styles.css";

const useStyles = makeStyles({
    root: {
      boxShadow: '0 0 6px 6px rgba(200, 200, 200, .3)',
      width:"400px", 
      margin:"auto", 
      padding: "20px"
    },
    title: {
        fontSize: '30px',
        fontWeight:'bold',
        marginTop: '20px',
        marginBottom:"30px",
    },
    label: {
        marginTop: '20px',
        fontSize: '20px',
    },
    input: {
        fontSize: '16px',
        width: '100%',
        padding: '6px',
        marginTop: '10px',
        marginBottom:'10px'
    },
    sumbit: {
        marginTop: '20px',
        textAlign:'center',
    },
    error: {
        color: 'red'
    },
    responseError: {
        textAlign: 'left',
        padding:'10px',
        backgroundColor: '#F8D7DA',
        color: 'black'
    },
    responseSuccess: {
        textAlign: 'left',
        padding:'10px',
        backgroundColor: '#4CAF50',
        color: '#FFFFFF'
    }
  });


const ForgetComponent = (props) => {
    const classes = useStyles();
    const [successStatus, setSuccessStatus] = useState(0)
 
    return (
      <Fragment>
        <HeaderComponent />
        <div style={{paddingTop: '170px', paddingLeft:"10%", paddingRight:"10%", paddingBottom:'20px' }}>
            <div className={classes.root} >
            <div className={classes.title} >Forget Password</div>
                <Formik
                    initialValues={{
                        email: '',
                    }}
                    validationSchema={Yup.object().shape({
                        email: Yup.string().required("Email is reuqired").email('Invalid email'),
                    })}
                    onSubmit={({ email }, { setStatus, setSubmitting }) => {
                        setStatus();
                        MPTL2.DCW8G(email).then(resp => {
                            setSuccessStatus(1)
                        }, error => { setSuccessStatus(-1) });
                    }}
                    render={({ errors, status, touched, isSubmitting }) => (
                        <Form>                          
                            <div>
                                <span className={classes.label}>Email</span>
                            </div>
                            <div>
                                <Field name="email" type="text" className={classes.input} />
                            </div>
                            <div>
                                <ErrorMessage name="email" component="div" className={classes.error} />
                            </div>

                            <div style={{marginTop:'20px'}}>
                                <span className={classes.label}>Please enter your email, we will send the reset code to your email.</span>
                            </div>

                            <div className={classes.sumbit}>
                                <Button type="submit" variant="contained" color="primary" style={{width:'100%', textTransform:'capitalize', backgroundColor:'#0B74A4', color:'#FFFFFF'}}>
                                    Send reset code
                                </Button>
                            </div>
                            <div className={classes.sumbit}>
                            {successStatus === 1 && <div className={classes.responseSuccess}>Reset code has beed send to your email</div>}
                            {successStatus === -1 && <div className={classes.responseError}>Fail to send the reset code</div>}
                            </div>                            

                        </Form>
                    )}
                />                

            </div>
        </div>
        <FooterComponent />
      </Fragment>   
    );

}

export default withRouter(ForgetComponent);

