
export function HPEQO(TL63D) {
    this.S58HW = TL63D.S58HW;
    this.OT1BU = TL63D;

    this.GBNM5 = function() {
        this.FYN0F = this.S58HW.createBuffer();
        this.S58HW.bindBuffer(this.S58HW.ARRAY_BUFFER, this.FYN0F);

        let BM52A = [
             -0.5, 0.5, 0.0,
             0.5, 0.5, 0.0,
             0.5, -0.5, 0.0,
             -0.5, -0.5, 0.0
        ];

        this.S58HW.bufferData(this.S58HW.ARRAY_BUFFER, new Float32Array(BM52A), this.S58HW.STATIC_DRAW);
        this.FYN0F.itemSize = 3;
        this.FYN0F.numItems = 4;
    }

    this.KCBLT = function(VJL7U, AU4VN) {
        this.S58HW.uniformMatrix4fv(this.OT1BU.HAM9K, false, VJL7U);
        this.S58HW.disableVertexAttribArray(this.OT1BU.J452X);
        this.S58HW.vertexAttrib4f(this.OT1BU.J452X, AU4VN[0], AU4VN[1], AU4VN[2], AU4VN[3]);

        this.S58HW.bindBuffer(this.S58HW.ARRAY_BUFFER, this.FYN0F);
        this.S58HW.vertexAttribPointer(this.OT1BU.S047G, this.FYN0F.itemSize, this.S58HW.FLOAT, false, 0, 0);
        this.S58HW.drawArrays(this.S58HW.TRIANGLE_FAN, 0, 4);
    }
}
